import React, { useState, useEffect, useCallback } from "react";
import { connect } from "react-redux";
import styled from "styled-components";
import { withNamespaces } from "react-i18next";

import MonthShedule from "../../../assets/slide-2.png";
import RetinaMonthShedule from "../../../assets/slide-2@2x.png";
import {
  PlanCover,
  ResponsiveImage,
  PurposeList,
} from "../../../common/StyledComponents";
import CoverPlanList from "./CoverPlanList";
import {
  getTasksorSubtasksForDifferentPeriods,
  fullMonthDate,
} from "../../../common/helpers";
import PeriodHeader from "./PeriodHeader";

const MonthPlan = ({
  healthTasks,
  learningTasks,
  mentalTasks,
  relationshipTasks,
  affairsTasks,
  t
}) => {
  const [taskVisible, setTaskVisible] = useState(undefined);
  const [list, setList] = useState([]);

  const updateSubtasks = useCallback(() => {
    setList(
      getTasksorSubtasksForDifferentPeriods(
        healthTasks,
        learningTasks,
        mentalTasks,
        relationshipTasks,
        affairsTasks,
        fullMonthDate
      )
    );
  }, [
    healthTasks,
    learningTasks,
    mentalTasks,
    relationshipTasks,
    affairsTasks,
  ]);

  useEffect(() => {
    updateSubtasks();
  }, [updateSubtasks]);

  const { month } = t("time", { returnObjects: true });

  return (
    <PlanCover hovering={taskVisible ? String(taskVisible) : undefined}>
      <div className="planCover">
        <PurposeList>
          <PeriodHeader period={month} tasksLength={list.length} />
          <CoverPlanList
            period="month"
            list={list}
            taskVisible={taskVisible}
            setTaskVisible={setTaskVisible}
            exactDate={fullMonthDate}
            updateSubtasks={updateSubtasks}
          />
        </PurposeList>
      </div>
      <MonthImage
        src={MonthShedule}
        srcSet={`${MonthShedule},
            ${RetinaMonthShedule} 2x`}
        alt="logo"
      />
    </PlanCover>
  );
};

const MonthImage = styled(ResponsiveImage)`
  height: calc(31vh - 10px);
  border-radius: 10px;
`;

const mapStateToProps = (state) => ({
  healthTasks: state.healthTasks.month,
  learningTasks: state.learningTasks.month,
  mentalTasks: state.mentalTasks.month,
  relationshipTasks: state.relationshipTasks.month,
  affairsTasks: state.affairsTasks.month,
});

export default withNamespaces()(connect(mapStateToProps)(MonthPlan));
