import React from "react";
import Expand from "react-expand-animated";
import { withNamespaces } from "react-i18next";

import { AddTaskButton } from "../../../../common/StyledComponents";
import { ReactComponent as AddIcon } from "../../../../assets/add.svg";
import {transitions} from '../../../../common/helpers';

const ExpandableAddTaskButton = ({isTaskFormOpen, inputTask, t}) => {
  const {addAim} = t("aim", { returnObjects: true });
  return (
    <Expand open={!isTaskFormOpen} duration={300} transitions={transitions}>
      <AddTaskButton type="link" onClick={inputTask}>
        <AddIcon />
        {addAim}
      </AddTaskButton>
    </Expand>
  );
};
export default withNamespaces()(ExpandableAddTaskButton);
