import React from "react";
import { Progress, Row, Col } from "antd";
import styled from "styled-components";

const ProgressLine = () => (
  <Row gutter={[0, 0]}>
    <Col span={24}>
      <ColoredProgress
        strokeColor={{
          "0%": "#FD7A58",
          "73%": "#F85C71",
          "100%": "#FCB4CE ",
        }}
        percent={0}
      />
    </Col>
  </Row>
);

const ColoredProgress = styled(Progress)`
  text-align: center;
  div {
    display: flex;
    height: 8px;
    align-items: center;
  }
  .ant-progress-text {
    color: #fff;
    display: block;
    font-size: 20px;
  }
  .ant-progress-outer {
    padding-right: 9%;
  }
  .ant-progress-inner {
    background: #f4f4f4;
  }
`;

export default ProgressLine;
