import React from "react";
import { VictoryChart, VictoryLine, VictoryAxis } from "victory";
import styled from "styled-components";

const Chart = ({ openResultsList }) => {
  return (
    <VictoryChartWithFont width={600} height={470} scale={{ x: "time" }}>
      <VictoryAxis
        crossAxis
        style={{
          grid: { stroke: "#707070", strokeWidth: 1 }
        }}
      />
      <VictoryAxis
        dependentAxis
        crossAxis
        style={{
          grid: { stroke: "#707070", strokeWidth: 1 }
        }}
      />
      <VictoryLine
        events={[
          {
            target: "parent",
            eventHandlers: {
              onClick: () => {
                return [
                  {
                    target: "data",
                    eventKey: "all",
                    mutation: () => {
                      openResultsList && openResultsList();
                    }
                  }
                ];
              }
            }
          }
        ]}
        style={{
          data: { stroke: "#F85C71", strokeWidth: 4, cursor: "pointer" }
        }}
        data={[
          { x: new Date(1967, 1, 1), y: 3 },
          { x: new Date(1970, 1, 1), y: 4 },
          { x: new Date(1972, 1, 1), y: 3.7 },
          { x: new Date(1979, 1, 1), y: 5.3 },
          { x: new Date(1991, 1, 1), y: 0.8 },
          { x: new Date(1995, 1, 1), y: 3 },
          { x: new Date(2003, 1, 1), y: 4 },
          { x: new Date(2007, 1, 1), y: 3 },
          { x: new Date(2020, 1, 1), y: 7 }
        ]}
      />
    </VictoryChartWithFont>
  );
};

const VictoryChartWithFont = styled(VictoryChart)`
  border: 200px solid red;
`;

export default Chart;
