import React, { useState, useEffect, useCallback } from "react";
import { Col } from "antd";
import moment from "moment";
import { connect } from "react-redux";
import { useHistory, useLocation } from "react-router-dom";
import { v4 as uuid } from "uuid";
import * as R from "ramda";
import Expand from "react-expand-animated";
import EmptyTasks from '../common/EmptyTasks';

import {
  RightDirectionCol,
  BlackText,
  CardWithBorderRadius,
  PageTitle,
  ColWithRightTextAlign,
  LittleBlackText,
  OverflowContent,
  CenteredRow,
} from "../../../../common/StyledComponents";
import TaskCard from "../common/TaskCard/TaskCard";
import EverydayTasksForm from "./EverydayTasksForm";
import TasksSummaryInIcons from "../../../../components/TasksLengthRow/TasksSummaryInIcons";
import { switchTasksSphere } from "../common/helpers";
import AddTaskCard from "../common/AddTaskForm/AddTaskCard";
import ExpandableAddTaskButton from "../common/ExpandableAddTaskButton";
import { transitions } from "../../../../common/helpers";

const SelectedDayPage = ({
  day,
  year,
  monthData,
  healthTasks,
  learningTasks,
  mentalTasks,
  relationshipTasks,
  affairsTasks,
  sphere,
  pageTitle
}) => {
  const history = useHistory();
  const { pathname } = useLocation();
  const selectedDay = moment(day).format("DD");

  const generalDate = `${selectedDay} ${monthData.monthInGenetiveCase} ${year}`;

  const currentSphere = switchTasksSphere(
    sphere,
    healthTasks.day,
    learningTasks.day,
    mentalTasks.day,
    relationshipTasks.day,
    affairsTasks.day
  );

  const allDayTasks = currentSphere[generalDate]
    ? currentSphere[generalDate]
    : [];

  const tasksFromAllPeriods = switchTasksSphere(
    sphere,
    healthTasks,
    learningTasks,
    mentalTasks,
    relationshipTasks,
    affairsTasks
  );

  const getFlatSubtasksList = useCallback(
    (tasksList) => {
      let allPeriodsSubtasks = [];
      R.forEachObjIndexed(
        R.forEachObjIndexed((value, key) => {
          if (
            key === String(year) ||
            key === generalDate ||
            new RegExp(
              `${monthData.month} ${year}|${monthData.month} ${year} .+ Неделя`
            ).test(key)
          ) {
            allPeriodsSubtasks.push(value.map((task) => task.subtasks));
          }
        }),
        tasksList
      );
      const flatSubtasks = R.flatten(allPeriodsSubtasks);
      return flatSubtasks;
    },
    [generalDate, monthData.month, year]
  );

  const [currentTasks, setCurrentTasks] = useState(
    allDayTasks.filter((task) => task.title !== "Ежедневные задачи")
  );
  const [everydaySubtasks, setEverydaySubtasks] = useState(
    getFlatSubtasksList(tasksFromAllPeriods).filter(
      (subtask) => subtask.isEveryday === true
    )
  );
  const [isTaskFormOpen, setTaskFormOpen] = useState(false);
  const [taskToEdit, setTaskToEdit] = useState(undefined);

  if (!day) {
    history.push(`${pathname}`);
  }

  const handleUpdateDayTasks = useCallback(() => {
    const allDayTasks = currentSphere[generalDate]
      ? currentSphere[generalDate]
      : [];
    setCurrentTasks(
      allDayTasks.filter((task) => task.title !== "Ежедневные задачи")
    );
    const tasksFromAllPeriods = switchTasksSphere(
      sphere,
      healthTasks,
      learningTasks,
      mentalTasks,
      relationshipTasks,
      affairsTasks
    );

    setEverydaySubtasks(
      getFlatSubtasksList(tasksFromAllPeriods).filter(
        (subtask) => subtask.isEveryday === true
      )
    );
  }, [
    currentSphere,
    getFlatSubtasksList,
    sphere,
    healthTasks,
    learningTasks,
    mentalTasks,
    relationshipTasks,
    affairsTasks,
    generalDate,
  ]);

  useEffect(() => {
    handleUpdateDayTasks();
  }, [
    handleUpdateDayTasks,
    getFlatSubtasksList,
    isTaskFormOpen,
    sphere,
    healthTasks,
    learningTasks,
    mentalTasks,
    relationshipTasks,
    affairsTasks,
    currentSphere,
    generalDate,
    taskToEdit,
  ]);

  const inputTask = () => {
    setTaskFormOpen(true);
  };

  const isMonthActual = monthData.count >= moment().format('MM');

  return (
    <>
      <Col span={24}>
        <PageTitle>{pageTitle}</PageTitle>
      </Col>
      <CenteredRow>
        <Col offset={1} span={11}>
          {isMonthActual && <ExpandableAddTaskButton
            isTaskFormOpen={isTaskFormOpen}
            inputTask={inputTask}
          />}
        </Col>
        <ColWithRightTextAlign span={3}>
          <TasksSummaryInIcons
            tasksLength={currentTasks ? currentTasks.length : 0}
            subtasksLength={0}
            doneTasksLength={0}
          />
        </ColWithRightTextAlign>
        <ColWithRightTextAlign span={9}>
          <BlackText>
            {monthData.month} {year}
          </BlackText>
        </ColWithRightTextAlign>
      </CenteredRow>
      <RightDirectionCol offset={18} span={6}>
        <LittleBlackText>
          {selectedDay} {monthData.monthInGenetiveCase}
        </LittleBlackText>
      </RightDirectionCol>

      <OverflowContent offset={1} span={23} style={{ marginTop: 20 }}>
        <Col offset={9} span={15} style={{ marginBottom: 20 }}>
          <EverydayTasksForm list={everydaySubtasks} />
        </Col>
        <Col span={24}>
          <Expand
            open={isTaskFormOpen || Boolean(taskToEdit) }
            duration={300}
            transitions={transitions}
          >
            <AddTaskCard
              setvisible={setTaskFormOpen}
              sphere={sphere}
              period="day"
              day={generalDate}
              taskToEdit={taskToEdit}
              setTaskToEdit={setTaskToEdit}
            />
          </Expand>
        </Col>
        <Col span={24}>
          {R.isEmpty(currentTasks) ? (
            <EmptyTasks />
          ) : (
            currentTasks.map(
              (task) =>
                (taskToEdit && taskToEdit.id) !== task.id && (
                  <CardWithBorderRadius key={uuid()}>
                    <TaskCard
                      task={task}
                      sphere={sphere}
                      day={generalDate}
                      updateDayTasks={handleUpdateDayTasks}
                      taskToEditId={taskToEdit ? taskToEdit.id : ""}
                      setTaskToEdit={setTaskToEdit}
                    />
                  </CardWithBorderRadius>
                )
            )
          )}
        </Col>
      </OverflowContent>
    </>
  );
};

const mapStateToProps = (state) => ({
  healthTasks: state.healthTasks,
  learningTasks: state.learningTasks,
  mentalTasks: state.mentalTasks,
  relationshipTasks: state.relationshipTasks,
  affairsTasks: state.affairsTasks,
});

export default connect(mapStateToProps)(SelectedDayPage);
