import React from "react";
import styled from "styled-components";
import { Row, Col, List } from "antd";
import { withNamespaces } from "react-i18next";

import { ProfileCard } from "../../../../common/StyledComponents";

const Stats = ({ t }) => {
  const {
    subscribers,
    subscriptions,
    achieved,
    teachers,
    contribution,
    goals,
  } = t("profile", {
    returnObjects: true,
  });
  const listWithUserStats = [
    { title: subscribers, bordercolor: "#F85C71", value: 70 },
    { title: subscriptions, bordercolor: "#F85C71", value: 100 },
    { title: goals, bordercolor: "#1D4E89", value: 10 },
    { title: achieved, bordercolor: "#1D4E89", value: 15 },
    { title: contribution, bordercolor: "#2AD2C9", value: "15 000 $" },
    { title: teachers, bordercolor: "#2AD2C9", value: 3 },
  ];
  return (
    <ProfileCard>
      <Row>
        <List
          dataSource={listWithUserStats}
          grid={{
            gutter: 16,
            xs: 1,
            sm: 2,
          }}
          renderItem={(item) => (
            <List.Item>
              <Col span={12}>
                <StatisticTitle>{item.title}</StatisticTitle>
                <BoldText bordercolor={item.bordercolor}>{item.value}</BoldText>
              </Col>
            </List.Item>
          )}
        />
      </Row>
    </ProfileCard>
  );
};

const BoldText = styled.span`
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: 600;
  font-size: 18px;
  letter-spacing: -2px;
  color: #3b3b3b;
  padding-left: 5px;
  padding-right: 9px;
  border-left: ${(prop) => `1px solid ${prop.bordercolor}`};
`;

const StatisticTitle = styled.p`
  font-size: 14px;
  letter-spacing: -1px;
  line-height: 18px;
  margin: 10px 0;
`;

export default withNamespaces()(Stats);
