import React, { useState } from "react";
import { Col, Row, Form } from "antd";
import styled from "styled-components";
import { withNamespaces } from "react-i18next";

import {
  AddTaskTitle,
  NormalText,
} from "../../../../../../common/StyledComponents";
import { ReactComponent as EverydayTaskIcon } from "../../../../../../assets/everyDay.svg";
import DeadlineCalendar from "../../../../../../components/Calendar/DeadlineCalendar";

const DeadlineForm = ({
  period,
  timeLeft,
  setTimeLeft,
  deadline,
  setDeadline,
  t,
}) => {
  const [isRangeCalendarOpen, setRangeCalendarOpen] = useState(
    false
  );
  const { setTerms, deadlineDate, days } = t("aim", { returnObjects: true });

  return (
    <Col offset={1} span={23} style={{ marginTop: 10 }}>
      <AddTaskTitle>{setTerms}</AddTaskTitle>
      <Form style={{ marginTop: 30, marginBottom: 40 }}>
        <Row style={{ marginBottom: 25 }}>
          <Col span={18} onClick={() => setRangeCalendarOpen(true)}>
            <NormalText>
              {deadlineDate}: {deadline}
              <EverydayTaskIcon style={{ marginLeft: 15 }} />
            </NormalText>
          </Col>
          <Col offset={1} span={5}>
            <NormalText>
              <BottomBorderedText>{timeLeft}</BottomBorderedText> {days}
            </NormalText>
          </Col>
        </Row>
      </Form>
      <DeadlineCalendar
        setDeadline={setDeadline}
        setTimeLeft={setTimeLeft}
        isRangeCalendarOpen={isRangeCalendarOpen}
        setRangeCalendarOpen={setRangeCalendarOpen}
        numberOfMonths={period === "year" ? 2 : 1}
      />
    </Col>
  );
};

const BottomBorderedText = styled.span`
  border-bottom: 1px solid #3b3b3b;
  font-size: 28px;
`;

export default withNamespaces()(DeadlineForm);
