import React, { useState, useEffect } from "react";
import { Row, Col } from "antd";
import { withNamespaces } from "react-i18next";
import * as R from "ramda";

import { PageTitle } from "../../common/StyledComponents";
import SelectedYearPage from "./components/Year/SelectedYearPage";
import SelectedMonthPage from "./components/Month/SelectedMonthPage";
import SelectedWeekPage from "./components/Week/SelectedWeekPage";
import SelectedDayPage from "./components/Day/SelectedDayPage";
import SpherePageLayout from "./components/common/SphereLayout/SpherePageLayout";
import { useQuery } from "../../common/helpers";
import GlobalAimPage from "./components/GlobalAimPage/GlobalAimPage";

const SpherePage = ({ sphere, t }) => {
  const [year, setYear] = useState("");
  const [monthData, setMonth] = useState("");
  const [week, setWeek] = useState([]);
  const [day, setDay] = useState("");
  const [weekNumber, setWeekNumber] = useState("");

  const query = useQuery();
  const period = query.get("period");

  useEffect(() => {
    setDay("");
    setMonth("");
    setWeek([]);
  }, [year]);

  const pageTitle = R.prop(sphere, t("sphere", { returnObjects: true }));

  return (
    <Row>
      {!period ? (
        <>
          <Col span={24}>
            <PageTitle>
              {pageTitle}
            </PageTitle>
          </Col>
          <GlobalAimPage setYear={setYear} />
        </>
      ) : (
        <SpherePageLayout
          monthData={monthData}
          year={year}
          day={day}
          week={week}
          setMonth={setMonth}
          setWeek={setWeek}
          setDay={setDay}
          setWeekNumber={setWeekNumber}
        >
          {period === "year" && (
            <SelectedYearPage
              year={year}
              monthData={monthData}
              week={week}
              sphere={sphere}
              pageTitle={pageTitle}
            />
          )}
          {period === "month" && (
            <SelectedMonthPage
              year={year}
              monthData={monthData}
              week={week}
              sphere={sphere}
              setMonth={setMonth}
              pageTitle={pageTitle}
            />
          )}
          {period === "week" && (
            <SelectedWeekPage
              year={year}
              week={week}
              monthData={monthData}
              day={day}
              sphere={sphere}
              weekNumber={weekNumber}
            />
          )}
          {period === "day" && (
            <SelectedDayPage
              year={year}
              week={week}
              monthData={monthData}
              day={day}
              sphere={sphere}
              pageTitle={pageTitle}
            />
          )}
        </SpherePageLayout>
      )}
    </Row>
  );
};

export default withNamespaces()(SpherePage);
