import React, { Suspense } from "react";
import { Spin } from "antd";
import styled from "styled-components";

import { SPHERESLIST } from "../common/helpers";
import Home from "../pages/Home/Home";
import Profile from "../pages/Profile/Profile";
import SpherePage from "../pages/Sphere/SpherePage";
import Charts from "../pages/Charts/Charts";
import Settings from "../pages/Settings/Settings";

const LazyRegistration = React.lazy(() =>
  import("../pages/Registration/Registration")
);

const CentralSuspense = styled(Spin)`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
`;

export const enterRoute = {
  pathname: "/",
  component: (
    <Suspense fallback={<CentralSuspense size="large" />}>
      <LazyRegistration />
    </Suspense>
  ),
  exact: true,
};

const routes = [
  {
    pathname: "/profile",
    component: <Profile />,
    exact: false,
  },
  {
    pathname: "/charts",
    component: <Charts />,
    exact: false,
  },
  ...SPHERESLIST.map((item) => ({
    pathname: item.path,
    component: <SpherePage sphere={item.sphere} />,
    exact: false,
  })),
  {
    pathname: "/settings",
    component: <Settings />,
    exact: false,
  },
  {
    pathname: "/",
    component: <Home />,
    exact: true,
  },
  {
    component: <Home />,
  },
];

export default routes;
