import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "antd/dist/antd.css";
import { connect } from "react-redux";
import { v4 as uuidv4 } from "uuid";
// import * as R from "ramda";

import Layout from "./components/Layout/Layout";
import routes, { enterRoute } from "./routes/routes";
import "./App.css";

const App = ({ isUserAuthenticated }) => {
  const hasUserEntered = JSON.parse(localStorage.getItem("user")) || isUserAuthenticated;

  return (
    <Router>
      {!hasUserEntered ? (
        <Route key={uuidv4()}>{enterRoute.component}</Route>
      ) : (
        <Layout>
          <Switch>
            {routes.map((route) => (
              <Route key={uuidv4()} path={route.pathname}>
                {route.component}
              </Route>
            ))}
            <Route key={uuidv4()}>{enterRoute.component}</Route>
          </Switch>
        </Layout>
      )}
    </Router>
  );
};

const mapStateToProps = (state) => ({
  isUserAuthenticated: state.auth.isUserAuthenticated,
});

export default connect(mapStateToProps)(App);
