import React, { useState, useEffect } from "react";
import { Col } from "antd";
import { connect } from "react-redux";
import { v4 as uuid } from "uuid";
import { useHistory, useLocation } from "react-router-dom";
import * as R from "ramda";
import Expand from "react-expand-animated";
import moment from "moment";

import {
  BlackText,
  CardWithShadow,
  Sticky,
  PageTitle,
  OverflowContent,
  ColWithRightTextAlign,
  CenteredRow,
} from "../../../../common/StyledComponents";
import TaskCard from "../common/TaskCard/TaskCard";
import TasksSummaryInIcons from "../../../../components/TasksLengthRow/TasksSummaryInIcons";
import AddTaskCard from "../common/AddTaskForm/AddTaskCard";
import { switchTasksSphere, updateTasks } from "../common/helpers";
import ExpandableAddTaskButton from "../common/ExpandableAddTaskButton";
import { transitions } from "../../../../common/helpers";
import EmptyTasks from '../common/EmptyTasks';

const SelectedMonthPage = ({
  year,
  monthData,
  sphere,
  healthTasks,
  learningTasks,
  mentalTasks,
  relationshipTasks,
  affairsTasks,
  setMonth,
  pageTitle
}) => {
  const history = useHistory();
  const { pathname } = useLocation();

  const currentSphere = switchTasksSphere(
    sphere,
    healthTasks.month,
    learningTasks.month,
    mentalTasks.month,
    relationshipTasks.month,
    affairsTasks.month
  );

  const generalDate = `${monthData.month} ${year}`;

  const [isTaskFormOpen, setTaskFormOpen] = useState(false);
  const [currentTasks, setCurrentTasks] = useState(
    currentSphere[generalDate] ? currentSphere[generalDate] : []
  );
  const [taskToEdit, setTaskToEdit] = useState(undefined);

  if (R.isEmpty(monthData)) {
    history.push(`${pathname}`);
  }

  const inputTask = () => {
    setTaskFormOpen(true);
  };

  useEffect(() => {
    updateTasks(setCurrentTasks, currentSphere, generalDate);
  }, [
    healthTasks,
    learningTasks,
    mentalTasks,
    relationshipTasks,
    affairsTasks,
    currentSphere,
    generalDate,
    taskToEdit,
  ]);

  const handleYearClick = () => {
    setMonth("");
    history.push(`${pathname}?period=year`);
  };

  const isMonthActual = monthData.count >= moment().format("MM");

  return (
    <>
      <Sticky>
        <Col span={24}>
          <PageTitle>{pageTitle}</PageTitle>
        </Col>
        <CenteredRow>
          <Col offset={1} span={11}>
            {isMonthActual && (
              <ExpandableAddTaskButton
                isTaskFormOpen={isTaskFormOpen}
                inputTask={inputTask}
              />
            )}
          </Col>
          <ColWithRightTextAlign span={3}>
            <TasksSummaryInIcons
              tasksLength={currentTasks ? currentTasks.length : 0}
              subtasksLength={0}
              doneTasksLength={0}
            />
          </ColWithRightTextAlign>
          <ColWithRightTextAlign span={9}>
            <BlackText>
              {monthData.month}{" "}
              <BlackText
                style={{ cursor: "pointer" }}
                onClick={handleYearClick}
              >
                {year}
              </BlackText>
            </BlackText>
          </ColWithRightTextAlign>
        </CenteredRow>
      </Sticky>

      <OverflowContent
        offset={1}
        span={23}
        style={{ marginTop: "5vh"}}
      >
        <Expand
          open={isTaskFormOpen || Boolean(taskToEdit)}
          duration={300}
          transitions={transitions}
        >
          <AddTaskCard
            setvisible={setTaskFormOpen}
            sphere={sphere}
            period="month"
            month={generalDate}
            taskToEdit={taskToEdit}
            setTaskToEdit={setTaskToEdit}
          />
        </Expand>
        {R.isEmpty(currentTasks) && !isTaskFormOpen && !Boolean(taskToEdit) ? (
          <EmptyTasks />
        ) : (
          currentTasks.map(
            (task) =>
              (taskToEdit && taskToEdit.id) !== task.id && (
                <CardWithShadow
                  key={uuid()}
                  onClick={() =>
                    updateTasks(setCurrentTasks, currentSphere, generalDate)
                  }
                >
                  <TaskCard
                    task={task}
                    sphere={sphere}
                    month={generalDate}
                    setTaskToEdit={setTaskToEdit}
                  />
                </CardWithShadow>
              )
          )
        )}
      </OverflowContent>
    </>
  );
};

const mapStateToProps = (state) => ({
  healthTasks: state.healthTasks,
  learningTasks: state.learningTasks,
  mentalTasks: state.mentalTasks,
  relationshipTasks: state.relationshipTasks,
  affairsTasks: state.affairsTasks,
});

export default connect(mapStateToProps)(SelectedMonthPage);
