import React from "react";
import styled from "styled-components";
import { Menu, Layout } from "antd";
import { NavLink, useLocation } from "react-router-dom";

import { ReactComponent as HealthIcon } from "../../assets/health.svg";
import { ReactComponent as LearningIcon } from "../../assets/learning.svg";
import { ReactComponent as RelationshipIcon } from "../../assets/relationship.svg";
import { ReactComponent as AffairsIcon } from "../../assets/suitcase.svg";
import { ReactComponent as HomeIcon } from "../../assets/home.svg";
import { ReactComponent as MentalIcon } from "../../assets/mental.svg";

const { Sider } = Layout;

const Sidebar = () => {
  const { pathname } = useLocation();
  return (
    <SideSections width={80}>
      <PageSizeWrapper>
        {pathname !== "/" && (
          <HomeLink to="/">
            <HomeIcon />
          </HomeLink>
        )}
        <SidebarMenu pagelocation={pathname}>
          <Menu.Item key="health">
            <NavLink to="/health">
              <HealthIcon />
            </NavLink>
          </Menu.Item>

          <Menu.Item key="learning">
            <NavLink to="/learning">
              <LearningIcon />
            </NavLink>
          </Menu.Item>

          <Menu.Item key="mental">
            <NavLink to="/mental">
              <MentalIcon />
            </NavLink>
          </Menu.Item>

          <Menu.Item key="relationships">
            <NavLink to="/relationship">
              <RelationshipIcon />
            </NavLink>
          </Menu.Item>

          <Menu.Item key="affairs">
            <NavLink to="/affairs">
              <AffairsIcon />
            </NavLink>
          </Menu.Item>
        </SidebarMenu>
      </PageSizeWrapper>
    </SideSections>
  );
};
const HomeLink = styled(NavLink)`
  position: absolute;
  svg {
    margin-top: 20px;
    padding: 4px;
  }
`;

const PageSizeWrapper = styled.div`
  height: 100vh;
  display: flex;
  width: 80px;
  background: linear-gradient(180deg, #f85c71 0%, #1d4e89 100%);
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  position: fixed;
`;

const SidebarMenu = styled(Menu)`
  background: transparent;
  display: flex;
  align-self: center;
  flex-direction: column;
  justify-content: space-between;
  height: 450px;
  border: none;
  svg {
    fill: #f6eeec;
    padding: 3px;
  }
  li a {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  .ant-menu-item {
    height: 85px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  li.ant-menu-item.ant-menu-item-selected,
  li.ant-menu-item.ant-menu-item-active {
    background-color: transparent;
  }
`;

const SideSections = styled(Sider)`
  .ant-layout-sider-children {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
`;

export default Sidebar;
