import React from "react";
import styled from "styled-components";
import { Row, Col, Card, Avatar, Icon } from "antd";
import { withNamespaces } from "react-i18next";

import {
  ProfileCardTitle,
  SimpleButton,
  RightDirectionCol,
  FullSizeRow,
  RowCenteredCol,
} from "../../../../common/StyledComponents";
import { ReactComponent as BiggerListIcon } from "../../../../assets/list.svg";
import { ReactComponent as ClockIcon } from "../../../../assets/filledClock.svg";
import { SPHERESLIST } from "../../../../common/helpers";
import AchievementsCardAnimation from "./AchievementsCardAnimation";

const AchievementsList = ({ t }) => {
  const { myAchievements, tasks, share, run } = t("profile", {
    returnObjects: true,
  });
  const { days } = t("aim", {
    returnObjects: true,
  });
  return (
    <>
      <Col span={24}>
        <ProfileCardTitle>{myAchievements}</ProfileCardTitle>
      </Col>
      <OverflowRow>
        {[1, 2, 3, 4].map((item) => (
          <Col key={item} span={8}>
            <AchievementsCard>
              <AbsoluteContent>
                <MainAchievementContentRow>
                  <Col span={12}>
                    <AchievementsAvatar
                      size="large"
                      icon={SPHERESLIST[0].icon}
                    />
                  </Col>
                  <RightDirectionCol span={12}>
                    <SimpleButton>
                      <LittleDarkText>{share}</LittleDarkText>
                      <Icon type="share-alt" />
                    </SimpleButton>
                  </RightDirectionCol>
                  <Col span={24} style={{ marginTop: 20 }}>
                    <RowCenteredCol span={24}>
                      <ClockIcon
                        style={{
                          marginRight: 10,
                        }}
                      />
                      <LittleDarkText>67 {days}</LittleDarkText>
                    </RowCenteredCol>
                    <RowCenteredCol span={24}>
                      <BiggerListIcon
                        style={{
                          maxWidth: 9,
                          marginRight: 10,
                        }}
                      />
                      <LittleDarkText>100 {tasks}</LittleDarkText>
                    </RowCenteredCol>
                  </Col>
                </MainAchievementContentRow>
                <FooterCol span={24}>{run}</FooterCol>
              </AbsoluteContent>
              <AchievementsCardAnimation />
            </AchievementsCard>
          </Col>
        ))}
      </OverflowRow>
    </>
  );
};

const AbsoluteContent = styled(FullSizeRow)`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  position: absolute;
  z-index: 1;
`;

const MainAchievementContentRow = styled(Row)`
  padding: 15px 20px;
  flex: 1 0 auto;
`;

const LittleDarkText = styled.span`
  font-size: 11px;
  line-height: 11px;
  color: #3b3b3b;
  background: #fff;
`;

const FooterCol = styled(Col)`
  padding: 25px 20px;
  flex-shrink: 0;
  background: linear-gradient(
    180deg,
    rgba(248, 92, 113, 0.7) 0%,
    rgba(29, 78, 137, 0.7) 100%
  );
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  text-align: center;
  font-weight: 500;
  font-size: 16px;
  color: #ffffff;
`;

const AchievementsAvatar = styled(Avatar)`
  background: linear-gradient(180deg, #f85c71 0%, #1d4e89 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  svg {
    fill: #fff;
    max-height: 20px;
  }
`;

const AchievementsCard = styled(Card)`
  .ant-card-body {
    padding: 0;
  }
  overflow: hidden;
  border-radius: 20px;
  margin-right: 20px;
  max-height: 250px;
`;

const OverflowRow = styled(FullSizeRow)`
  display: flex;
  flex-wrap: no-wrap;
  overflow-x: auto;
  ::-webkit-scrollbar {
    width: 0px;
  }
`;

export default withNamespaces()(AchievementsList);
