import React, { useState, useEffect, useCallback } from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";

import WeekShedule from "../../../assets/slide-3.png";
import RetinaWeekShedule from "../../../assets/slide-3@2x.png";
import {
  PlanCover,
  ResponsiveImage,
  PurposeList,
} from "../../../common/StyledComponents";
import CoverPlanList from "./CoverPlanList";
import PeriodHeader from "./PeriodHeader";
import {
  getTasksorSubtasksForDifferentPeriods,
  currentWeek,
} from "../../../common/helpers";

const WeekPlan = ({
  healthTasks,
  learningTasks,
  mentalTasks,
  relationshipTasks,
  affairsTasks,
  t,
}) => {
  const [taskVisible, setTaskVisible] = useState(undefined);
  const [list, setList] = useState([]);

  const updateSubtasks = useCallback(() => {
    setList(
      getTasksorSubtasksForDifferentPeriods(
        healthTasks,
        learningTasks,
        mentalTasks,
        relationshipTasks,
        affairsTasks,
        currentWeek
      )
    );
  }, [
    healthTasks,
    learningTasks,
    mentalTasks,
    relationshipTasks,
    affairsTasks,
  ]);

  useEffect(() => {
    updateSubtasks();
  }, [updateSubtasks]);

  const { week } = t("time", { returnObjects: true });

  return (
    <PlanCover hovering={taskVisible ? String(taskVisible) : undefined}>
      <div className="planCover">
        <PurposeList>
          <PeriodHeader period={week} tasksLength={list.length} />
          <CoverPlanList
            period="week"
            list={list}
            taskVisible={taskVisible}
            setTaskVisible={setTaskVisible}
            exactDate={currentWeek}
            updateSubtasks={updateSubtasks}
          />
        </PurposeList>
      </div>
      <WeekImage
        src={WeekShedule}
        srcSet={`${WeekShedule},
            ${RetinaWeekShedule} 2x`}
        alt="logo"
      />
    </PlanCover>
  );
};

const WeekImage = styled(ResponsiveImage)`
  height: calc(29vh - 10px);
  border-radius: 10px;
`;

const mapStateToProps = (state) => ({
  healthTasks: state.healthTasks.week,
  learningTasks: state.learningTasks.week,
  mentalTasks: state.mentalTasks.week,
  relationshipTasks: state.relationshipTasks.week,
  affairsTasks: state.affairsTasks.week,
});

export default withNamespaces()(connect(mapStateToProps)(WeekPlan));
