import React from "react";
import { Row, Col } from "antd";
import styled from 'styled-components';

import MonthsSidebar from "./MonthsSidebar";

const SpherePageLayout = ({
  children,
  monthData,
  day,
  week,
  setMonth,
  setWeek,
  setDay,
  setWeekNumber
}) => {
  return (
    <Row>
      <Col span={17}>
      {children}
      </Col>
      <FixedCalendarCol span={5}>
        <MonthsSidebar
          visibleMonth={monthData}
          setVisibleMonth={setMonth}
          setWeek={setWeek}
          week={week}
          day={day}
          setDay={setDay}
          setWeekNumber={setWeekNumber}
        />
      </FixedCalendarCol>
    </Row>
  );
};

const FixedCalendarCol = styled(Col)`
  position: fixed;
  margin-top: 10vh;
  margin-left: 68%
`;

export default SpherePageLayout;
