import React from "react";

import { VerticalProgress } from "../../../../../common/StyledComponents";

const TaskVerticalProgress = ({ percent }) => {
  return (
    <VerticalProgress
      orientation="vertical"
      showInfo={false}
      strokeColor={{
        "0%": "#FD7A58",
        "73%": "#F85C71",
        "100%": "#FCB4CE "
      }}
      percent={percent}
    />
  );
};

export default TaskVerticalProgress;
