import React from "react";
import { List, Typography, Row, Col } from "antd";
import styled from "styled-components";
import { withNamespaces } from "react-i18next";

import { ThinText, SimpleButton } from "../../../common/StyledComponents";
import { ReactComponent as DoneIcon } from "../../../assets/done.svg";
import { ReactComponent as WrongIcon } from "../../../assets/wrong.svg";
import { ReactComponent as FlagIcon } from "../../../assets/resultFlag.svg";

const { Text } = Typography;

const ICON = {
  check: <DoneIcon />,
  flag: <FlagIcon />,
  close: <WrongIcon />,
};

const mockedData = [
  {
    text: "Пробежать 10 км",
    status: "check",
    date: "20.02.2019",
  },
  {
    text: "Пробежать 15 км",
    status: "close",
    date: "20.02.2019",
  },
  {
    text: "Прочитать 30 книг",
    status: "flag",
    date: "20.02.2019",
  },
  {
    text: "Открыл для себя новые возможности",
    status: "check",
    date: "20.02.2019",
  },
  {
    text: "Удачно сдать 3 проекта на работе",
    status: "check",
    date: "20.02.2019",
  },
];

const ResultsList = ({ t }) => {
  const { achievedGoal, more } = t("diary", { returnObjects: true });

  return (
    <Row>
      <BorderedList
        grid={{ gutter: 16, column: 1 }}
        dataSource={mockedData}
        renderItem={(item) => (
          <List.Item>
            <ItemRow>
              {item.status === "flag" && (
                <UpperTextCol span={23} offset={1}>
                  <UpperText>{achievedGoal}</UpperText>
                </UpperTextCol>
              )}
              <Col span={1}>{ICON[item.status]}</Col>
              <Col span={19}>
                <ThinText>{item.text}</ThinText>
              </Col>
              <Col span={4}>
                <ThinText>20.02.2019</ThinText>
              </Col>
            </ItemRow>
          </List.Item>
        )}
      />
      <Col span={3} offset={9}>
        <SimpleButton>
          <ThinText>{more}</ThinText>
        </SimpleButton>
      </Col>
    </Row>
  );
};

const UpperTextCol = styled(Col)`
  position: absolute;
  top: -30px;
`;

const UpperText = styled(Text)`
  letter-spacing: 0.64px;
  color: #1d4e89;
  font-size: 20px;
  font-weight: 400;
`;

const ItemRow = styled(Row)`
  display: flex;
  align-items: center;
`;

const BorderedList = styled(List)`
  margin-top: 15%;
  text-align: left;
  .ant-col .ant-list-item {
    border-bottom: 1px solid #b5b5b5;
    margin-top: 30px;
  }
`;

export default withNamespaces()(ResultsList);
