import React from "react";
import { Col, List, Icon } from "antd";
import styled from "styled-components";

import {
  RegularWideText,
  ThinRedText,
  ListWithMoreIcon,
  FullSizeRow
} from "../../../../common/StyledComponents";

const SubtasksList = ({ listToRender }) => {
  return (
    <ListWithMoreIcon
      dataSource={listToRender}
      renderItem={(item, index) => (
        <List.Item>
          <FullSizeRow>
            <Col span={1}>
              <RegularWideText>{index + 1}</RegularWideText>
            </Col>
            <Col span={20}>
              <RegularWideText>{item.plan}</RegularWideText>
            </Col>
            <Col span={2}>
              <ThinRedText>{item.date}</ThinRedText>
            </Col>
            <FlexColumn span={1}>
              <ColoredIcon type="more" />
            </FlexColumn>
          </FullSizeRow>
        </List.Item>
      )}
    />
  );
};

const FlexColumn = styled(Col)`
  display: flex;
  padding-left: 20;
  padding-top: 10;
  justify-content: center;
  align-items: center;
`;

const ColoredIcon = styled(Icon)`
  color: #f85c71;
  cursor: pointer;
  text-align: left;
`;

export default SubtasksList;
