import React from "react";
import styled from "styled-components";

import {
  TaskIconsWrapper,
} from "../../../../../../common/StyledComponents";
import { ReactComponent as NotificationIcon } from "../../../../../../assets/sconce.svg";
import { ReactComponent as BiggerListIcon } from "../../../../../../assets/list.svg";
import { ReactComponent as DollarIcon } from "../../../../../../assets/dollar.svg";
import {SWITCH_ICON} from '../../helpers';

const IconsCardSider = ({activeIcon, setActiveIcon, hasDeadline, hasSubtasks, hasSconce}) => {

  const toggleIcon = icon => {
    activeIcon === icon ? setActiveIcon("") : setActiveIcon(icon);
  };

  return (
    <AddTaskIconsWrapper>
      <ToggledNotificationIcon
        isactive={activeIcon === SWITCH_ICON.clock || hasDeadline ? "true" : undefined}
        onClick={() => toggleIcon(SWITCH_ICON.clock)}
      />
      <ToggledBiggerListIcon
        isactive={activeIcon === SWITCH_ICON.subtasks || hasSubtasks ? "true" : undefined}
        onClick={() => toggleIcon(SWITCH_ICON.subtasks)}
      />
      <ToggledDollarIcon
        isactive={activeIcon === SWITCH_ICON.dollar || hasSconce ? "true" : undefined}
        onClick={() => toggleIcon(SWITCH_ICON.dollar)}
      />
    </AddTaskIconsWrapper>
  );
};

const ToggledDollarIcon = styled(DollarIcon)`
  fill: ${prop => (Boolean(prop.isactive) ? "url(#gradient)" : "#bebdbd")};
  cursor: pointer;
`;

const ToggledBiggerListIcon = styled(BiggerListIcon)`
  fill: ${prop => (Boolean(prop.isactive) ? "url(#gradient)" : "#bebdbd")};
  cursor: pointer;
`;

const ToggledNotificationIcon = styled(NotificationIcon)`
  fill: ${prop => (Boolean(prop.isactive) ? "url(#gradient)" : "#bebdbd")};
  cursor: pointer;
`;

const AddTaskIconsWrapper = styled(TaskIconsWrapper)`
  padding: 15px 0 10px 0;
`;

export default IconsCardSider;
