import React from "react";
import { List } from "antd";

import { ThinText, SubTitle } from "../../../../common/StyledComponents";

const EverydayTasksForm = ({ list }) => {
  return (
    list.length > 0 && (
      <List
        header={<SubTitle>Ежедневные задачи</SubTitle>}
        dataSource={list}
        renderItem={item => (
          <List.Item>
            <ThinText>{item.plan}</ThinText>
          </List.Item>
        )}
      />
    )
  );
};

export default EverydayTasksForm;
