import React, { useState, useEffect, useCallback, useRef } from "react";
import { Col, Row, Input, Form, message, Divider } from "antd";
import styled from "styled-components";
import { connect } from "react-redux";
import { v4 as uuid } from "uuid";
import moment from "moment";
import Expand from "react-expand-animated";
import { withNamespaces } from "react-i18next";

import {
  AddTaskActionCreator,
  EditTaskActionCreator,
} from "../../../../../redux/actionCreators/TasksActionCreators";
import {
  Column,
  CardWithShadow,
  AddTaskTitle,
  PrimaryButton,
  CancelButton,
} from "../../../../../common/StyledComponents";
import SubtaskForm from "./components/SubtaskForm";
import IconsCardSider from "./components/IconsCardSider";
import { SWITCH_ICON } from "../helpers";
import DeadlineForm from "./components/DeadlineForm";
import SconceForm from "./components/SconceForm";

const transitions = ["height", "opacity"];

const AddTaskCard = ({
  setvisible,
  sphere,
  addTask,
  form,
  period,
  taskToEdit,
  setTaskToEdit,
  editTask,
  t,
  ...props
}) => {
  const [subtasks, setSubtasks] = useState([]);
  const [activeIcon, setActiveIcon] = useState(SWITCH_ICON.clock);
  const [isFreezeOpen, setFreezeOpen] = useState(false);
  const [timeLeft, setTimeLeft] = useState(0);
  const [deadline, setDeadline] = useState(
    moment().add(1).format("DD.MM.YYYY")
  );
  const [currency, setCurrency] = useState("");
  const [sconce, setSconce] = useState(0);
  const [card, setCard] = useState("");

  const { getFieldDecorator, validateFields, resetFields } = form;

  const {
    addNewAim,
    add,
    edit,
    cancel,
    inputTitle,
    inputTitleValidator,
    inputDescription,
    inputDecriptionValidator,
    setTerms,
    addSubtask,
  } = t("aim", { returnObjects: true });

  const exactDate = props[period];
  let currentYear = useRef("");
  const setDefaultDeadline = useCallback(() => {
    if (!taskToEdit) {
      switch (period) {
        case "year":
          setDeadline(
            moment(exactDate, "YYYY").endOf("year").format("DD.MM.YYYY")
          );
          currentYear.current = exactDate;
          break;
        case "month":
          setDeadline(
            moment(exactDate, "MMMM YYYY").endOf("month").format("DD.MM.YYYY")
          );
          currentYear.current = moment(exactDate, "MMMM YYYY").format("YYYY");
          break;
        case "week":
          //To FIX
          setDeadline(
            moment(exactDate.split(" ").slice(0, 2).join(" "), "MMMM YYYY")
              .endOf("week")
              .format("DD.MM.YYYY")
          );
          currentYear.current = moment(
            exactDate.split(" ").slice(0, 2).join(" "),
            "MMMM YYYY"
          ).format("YYYY");
          break;
        case "day":
          setDeadline(moment(exactDate, "DD MMMM YYYY").format("DD.MM.YYYY"));
          currentYear.current = moment(exactDate, "DD MMMM YYYY").format(
            "YYYY"
          );
          break;
        default:
          break;
      }
    }
  }, [exactDate, period, taskToEdit]);

  useEffect(() => {
    setDefaultDeadline();
  }, [setDefaultDeadline]);

  const handleSaveTask = () => {
    validateFields((err, values) => {
      if (!err) {
        if (!deadline) {
          message.error(setTerms);
        } else if (subtasks.length === 0) {
          message.error(addSubtask);
        } else {
          const newTask = {
            id: taskToEdit ? taskToEdit.id : uuid(),
            sphere,
            deadline,
            ...values,
            subtasks: subtasks.map((item) => ({
              ...item,
              deadline: item.isEveryday
                ? moment(currentYear.current, "YYYY")
                    .endOf("year")
                    .format("DD.MM.YYYY")
                : deadline,
            })),
            sconce,
            currency,
            card,
          };
          if (taskToEdit) {
            editTask(sphere, newTask, period, exactDate);
            setTaskToEdit(undefined);
          } else {
            addTask(sphere, newTask, period, exactDate);
          }
          setvisible(false);
          resetFields();
          setSubtasks([]);
          setCard("");
          setSconce(0);
          setCurrency("");
          setFreezeOpen(false);
          setActiveIcon(SWITCH_ICON.clock);
          setDefaultDeadline();
        }
      }
    });
  };

  const handleCancel = () => {
    setvisible(false);
    taskToEdit && setTaskToEdit(undefined);
    resetFields();
    setSubtasks([]);
    setCard("");
    setSconce(0);
    setCurrency("");
    setFreezeOpen(false);
    setDefaultDeadline();
    setActiveIcon(SWITCH_ICON.clock);
  };

  useEffect(() => {
    if (taskToEdit) {
      setDeadline(taskToEdit.deadline);
      setSubtasks(taskToEdit.subtasks);
      setCurrency(taskToEdit.currency);
      setSconce(taskToEdit.sconce);
      setCard(taskToEdit.card);
    }
  }, [taskToEdit]);

  useEffect(() => {
    setTimeLeft(moment(deadline, "DD.MM.YYYY").diff(moment(), "days"));
  }, [deadline, taskToEdit]);

  return (
    <Card>
      <AddTaskWrapper justify="space-around" align="middle">
        <Col offset={1} span={20}>
          <AddTaskTitle>{addNewAim}</AddTaskTitle>
          <Form style={{ marginTop: 30 }}>
            <FormItemWithRegularText>
              {getFieldDecorator("title", {
                initialValue: taskToEdit ? taskToEdit.title : "",
                rules: [
                  {
                    required: true,
                    message: inputTitleValidator,
                  },
                ],
              })(<Input placeholder={inputTitle} />)}
            </FormItemWithRegularText>

            <TaskTextWrapper>
              <FormItemWithRegularText>
                {getFieldDecorator("description", {
                  initialValue: taskToEdit ? taskToEdit.description : "",
                  rules: [
                    {
                      required: true,
                      message: inputDecriptionValidator,
                    },
                  ],
                })(<Input placeholder={inputDescription} />)}
              </FormItemWithRegularText>
            </TaskTextWrapper>
            <Form.Item>
              <PrimaryButton
                style={{
                  background:
                    deadline && subtasks.length ? "#205698" : "#bebdbd",
                  marginRight: 20,
                }}
                type="primary"
                onClick={handleSaveTask}
              >
                {taskToEdit ? edit : add}
              </PrimaryButton>
              <CancelButton onClick={handleCancel}>{cancel}</CancelButton>
            </Form.Item>
          </Form>
        </Col>

        <Column offset={1} span={1}>
          <IconsCardSider
            hasDeadline={Boolean(deadline)}
            hasSubtasks={subtasks.length > 0}
            hasSconce={sconce > 0}
            activeIcon={activeIcon}
            setActiveIcon={setActiveIcon}
          />
        </Column>
      </AddTaskWrapper>

      <Expand
        open={activeIcon === SWITCH_ICON.subtasks}
        duration={300}
        transitions={transitions}
      >
        <>
          <Col offset={1} span={20}>
            <ColoredDivider />
          </Col>
          <SubtaskForm
            sphere={sphere}
            setSubtasks={setSubtasks}
            subtasks={subtasks}
            getFieldDecorator={getFieldDecorator}
            validateFields={validateFields}
          />
        </>
      </Expand>

      <Expand
        open={activeIcon === SWITCH_ICON.dollar}
        duration={300}
        transitions={transitions}
      >
        <>
          <Col offset={1} span={20}>
            <ColoredDivider />
          </Col>
          <SconceForm
            currency={currency}
            setCurrency={setCurrency}
            sconce={sconce}
            setSconce={setSconce}
            card={card}
            setCard={setCard}
            isFreezeOpen={isFreezeOpen}
            setFreezeOpen={setFreezeOpen}
            getFieldDecorator={getFieldDecorator}
            validateFields={validateFields}
          />
        </>
      </Expand>

      <Expand
        open={activeIcon === SWITCH_ICON.clock}
        duration={300}
        transitions={transitions}
      >
        <>
          <Col offset={1} span={20}>
            <ColoredDivider />
          </Col>
          <DeadlineForm
            period={period}
            timeLeft={timeLeft}
            setTimeLeft={setTimeLeft}
            deadline={deadline}
            setDeadline={setDeadline}
          />
        </>
      </Expand>
    </Card>
  );
};

const ColoredDivider = styled(Divider)`
  background: rgba(190, 189, 189, 0.5);
`;

const FormItemWithRegularText = styled(Form.Item)`
  label {
    font-size: 17px;
    letter-spacing: 0;
    color: #3b3b3b;
  }
  input {
    padding-left: 0px;
    padding-right: 0px;
    font-size: 17px;
    line-height: 25px;
    color: #bebdbd;
    border: none;
    border-bottom: 2px solid #bebdbd;
  }
  input[type="text"] {
    font-size: 17px;
    line-height: 25px;
    color: #3b3b3b;
  }
`;

const AddTaskWrapper = styled(Row)`
  display: flex;
  align-items: middle;
`;

const TaskTextWrapper = styled.div`
  margin: 30px 0 30px 0;
`;

const Card = styled(CardWithShadow)`
  margin-top: 20px;
  .ant-card-body {
    padding: 30px 0 10px 0;
  }
`;

const WrappedAddTaskCard = Form.create({ name: "addTaskForm" })(AddTaskCard);

const mapDispatchToProps = (dispatch) => ({
  addTask: (sphere, newTask, period, task) =>
    dispatch(AddTaskActionCreator(sphere, newTask, period, task)),
  editTask: (sphere, task, period, date) =>
    dispatch(EditTaskActionCreator(sphere, task, period, date)),
});

export default withNamespaces()(
  connect(null, mapDispatchToProps)(WrappedAddTaskCard)
);
