import React, { useEffect } from "react";
import { Col, Row, Input, Form, Button, Switch, Select, message } from "antd";
import styled from "styled-components";
import MaskInput from "react-maskinput";
import { withNamespaces } from "react-i18next";

import {
  Column,
  AddTaskTitle,
  NormalText,
  LittleText,
} from "../../../../../../common/StyledComponents";

const { Option } = Select;

const SconceForm = ({
  setFreezeOpen,
  isFreezeOpen,
  getFieldDecorator,
  validateFields,
  currency,
  setCurrency,
  sconce,
  setSconce,
  card,
  setCard,
  t,
}) => {
  const {
    setFine,
    edit,
    ifDontGetInTime,
    amountWillBeDeducted,
    forSupport,
    cardNumber,
    amount,
    chooseCurrency,
    freeze,
    withoutFine,
    afterFreeze,
    fieldForFreeze,
  } = t("aim", { returnObjects: true });

  const handleFreeze = () => {
    validateFields((err, values) => {
      if (values.card && values.currency && values.sconce) {
        setCurrency(values.currency);
        setSconce(values.sconce);
        setCard(values.card);
        setFreezeOpen(true);
      } else {
        message.error(fieldForFreeze);
      }
    });
  };

  // const checkPrice = (rule, value, callback) => {
  //   if (!value || value > 0) {
  //     return callback(incorrectInput);
  //   }
  //   callback();
  // };
  useEffect(() => {
    card && currency && sconce && setFreezeOpen(true);
  }, [card, currency, sconce, setFreezeOpen]);

  return isFreezeOpen ? (
    <Col offset={1} span={22} style={{ marginTop: 10, marginBottom: 10 }}>
      <Row gutter={[0, 20]}>
        <Col span={24}>
          <NormalText>
            {ifDontGetInTime}
            <NormalText strong>{card}</NormalText>
            {amountWillBeDeducted}
            <NormalText strong>
              {sconce} {currency}
            </NormalText>
            {forSupport}
          </NormalText>
        </Col>
        <Col span={24}>
          <DarkSimpleButton onClick={() => setFreezeOpen(false)}>
            {edit}
          </DarkSimpleButton>
        </Col>
      </Row>
    </Col>
  ) : (
    <Col offset={1} span={23} style={{ marginTop: 10 }}>
      <AddTaskTitle>{setFine}</AddTaskTitle>
      <Form style={{ marginTop: 30, marginBottom: 30 }}>
        <Row>
          <Col span={3} style={{ width: "13.5%" }}>
            <NormalText style={{ whiteSpace: "nowrap" }}>
              {cardNumber}
            </NormalText>
          </Col>
          <Col span={5} style={{ width: "18.33%" }}>
            <Form.Item>
              {getFieldDecorator("card", {
                initialValue: card ? card : "",
              })(
                <CreditCardMaskInput
                  alwaysShowMask
                  maskChar="_"
                  mask="0000 0000 0000 0000"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Row>
              <Col offset={1} span={8}>
                <NormalText>{amount}</NormalText>
              </Col>
              <Col offset={1} span={6}>
                <Form.Item>
                  {getFieldDecorator("sconce", {
                    initialValue: sconce,
                  })(<ColoredInput />)}
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item>
                  {getFieldDecorator("currency", {
                    rules: [
                      {
                        required: true,
                        message: chooseCurrency,
                      },
                    ],
                    initialValue: "uah",
                  })(
                    <LittleSelect>
                      <Option value="uah">uah</Option>
                      <Option value="usd">usd</Option>
                      <Option value="eur">euro</Option>
                    </LittleSelect>
                  )}
                </Form.Item>
              </Col>
            </Row>
          </Col>
          <Column span={4}>
            <DarkSimpleButton onClick={handleFreeze}>{freeze}</DarkSimpleButton>
          </Column>
          <Col span={7}>
            <SwitchWrapper>
              <Switch defaultChecked />
              <SwitchText>{withoutFine}</SwitchText>
            </SwitchWrapper>
          </Col>
          <Col span={24}>
            <LittleText>{afterFreeze}</LittleText>
          </Col>
        </Row>
      </Form>
    </Col>
  );
};

const CreditCardMaskInput = styled(MaskInput)`
  text-align: center;
  border: none;
  font-size: 20px;
  line-height: 25px;
  color: #bebdbd;
  outline: none;
`;

const LittleSelect = styled(Select)`
  font-size: 25px;
  line-height: 29px;
  color: #000;
  font-weight: 300;
  border: none;
  .ant-select-selection--single {
    border: none;
  }
`;
const ColoredInput = styled(Input)`
  font-size: 22px;
  line-height: 25px;
  color: #f85c71;
  font-weight: 300;
  border: none;
  border-bottom: 2px solid #bebdbd;
`;

const DarkSimpleButton = styled(Button)`
  border: none;
  background: #fff;
  font-size: 25px;
  line-height: 29px;
  color: #1d4e89;
  padding: 0;
`;

const SwitchWrapper = styled.div`
  .ant-switch {
    height: 17px;
  }
  .ant-switch::after {
    height: 12px;
    width: 12px;
  }
  .ant-switch-checked {
    background-color: #f85c71;
  }
`;

const SwitchText = styled.span`
  padding-left: 20px;
  font-size: 22px;
  line-height: 25px;
  color: #bebdbd;
  font-weight: 300;
`;

export default withNamespaces()(SconceForm);
