import {
  Card,
  Typography,
  Button,
  Input,
  Progress,
  Col,
  Row,
  List,
  Icon,
  Form,
  Modal,
} from "antd";
import styled from "styled-components";

const { Text } = Typography;

export const PageTitle = styled.h2`
  text-align: left;
  font-weight: 200;
  color: #1d4e89;
  font-size: 50px;
  margin-bottom: 20px;
`;

export const PlanCover = styled(Row)`
  .planCover {
    overflow: hidden;
    border-radius: 10px;
    background: ${(prop) =>
      prop.hovering ? "rgba(26, 26, 26, 0.9)" : "transparent"};
    transition: 500ms ease-in-out;
    width: 100%;
    height: 100%;
    font-size: 22px;
    display: flex;
    flex-direction: column;
    position: absolute;
    cursor: pointer;
  }
  .ant-typography {
    color: ${(prop) => (prop.hovering ? "#fff" : "rgba(0, 0, 0, 0.65)")};
  }
  .ant-list-empty-text {
    opacity: 0;
  }
  .ant-list-items {
    height: 17vh;
    overflow: auto;
    padding-bottom: 10px;
    ::-webkit-scrollbar {
      width: 0px;
    }
  }
  :hover {
    .planCover {
      background: rgba(26, 26, 26, 0.9);
    }
    .ant-typography {
      color: #fff;
    }
    ul,
    svg,
    p {
      opacity: 1;
      transition: 500ms ease-in-out;
    }
  }
`;

