import React, { useState } from "react";
import { Menu } from "antd";
import { connect } from "react-redux";
import styled from "styled-components";
import { withNamespaces } from "react-i18next";

import {
  DeleteSubtaskActionCreator,
  DeleteTaskActionCreator,
} from "../../redux/actionCreators/TasksActionCreators";
import DeadlineCalendar from "../Calendar/DeadlineCalendar";

const PopoverMenu = ({
  activeSubtask,
  updateSubtasks,
  period,
  exactDate,
  hidePopover,
  deleteSubtask,
  deleteTask,
  t
}) => {
  const [isCalendarVisible, setCalendarVisible] = useState(false);
  // activeSubtask may be both task or subtask
  const isActiveSubtaskATask = Boolean(activeSubtask.subtasks);

  const handleDelete = async () => {
    if (isActiveSubtaskATask) {
      await deleteTask(
        activeSubtask.sphere,
        period,
        exactDate,
        activeSubtask.id
      );
    } else {
      await deleteSubtask(
        activeSubtask.sphere,
        activeSubtask,
        period,
        exactDate
      );
    }
  };

  const handleSubtaskDone = () => {
    handleDelete();
    updateSubtasks && updateSubtasks();
    hidePopover();
  };

  const handleSubtaskCancel = () => {
    handleDelete();
    updateSubtasks && updateSubtasks();
    hidePopover();
  };

  const handleCalendarOpen = () => {
    setCalendarVisible(true);
    hidePopover();
  };

  const { postpone, remove, ready } = t("aim", { returnObjects: true });

  return (
    <>
      <RoundedMenu>
        <Menu.Item key="done" onClick={handleSubtaskDone}>
          {ready}
        </Menu.Item>
        <Divider />
        <Menu.Item key="cancel" onClick={handleSubtaskCancel}>
          {remove}
        </Menu.Item>
        <Divider />
        <Menu.Item key="changeDate" onClick={handleCalendarOpen}>
          {postpone}
        </Menu.Item>
      </RoundedMenu>
      <DeadlineCalendar
        setDeadline={() => {}}
        setTimeLeft={() => {}}
        isRangeCalendarOpen={isCalendarVisible}
        setRangeCalendarOpen={setCalendarVisible}
        numberOfMonths={1}
      />
    </>
  );
};

const Divider = styled(Menu.Divider)`
  background-color: #e6e6e6;
`;

const RoundedMenu = styled(Menu)`
  background: #f8f8f8;
  border-radius: 13px;
  border: none;
  .ant-menu-item {
    font-weight: 300;
    font-size: 16px;
    line-height: 20px;
    padding: 0;
    height: 20px;
    margin: 10px 0;
    color: #3b3b3b;
  }
  .ant-menu-item:hover {
    color: #f85c71;
  }
  .ant-menu-item-selected {
    background-color: #f8f8f8;
  }
`;

const mapDispatchToProps = (dispatch) => ({
  deleteSubtask: (sphere, subtask, period, exactDate) =>
    dispatch(DeleteSubtaskActionCreator(sphere, subtask, period, exactDate)),
  deleteTask: (sphere, period, date, id) =>
    dispatch(DeleteTaskActionCreator(sphere, period, date, id)),
});

export default withNamespaces()(connect(null, mapDispatchToProps)(PopoverMenu));
