import React, { useState, useEffect } from "react";
import { Col } from "antd";
import { connect } from "react-redux";
import { v4 as uuid } from "uuid";
import { useHistory, useLocation } from "react-router-dom";
import * as R from "ramda";
import Expand from "react-expand-animated";
import moment from "moment";
import { withNamespaces } from "react-i18next";

import {
  BlackText,
  CardWithBorderRadius,
  Sticky,
  PageTitle,
  OverflowContent,
  ColWithRightTextAlign,
  LittleBlackText,
  CenteredRow,
} from "../../../../common/StyledComponents";
import TaskCard from "../common/TaskCard/TaskCard";
import TasksSummaryInIcons from "../../../../components/TasksLengthRow/TasksSummaryInIcons";
import AddTaskCard from "../common/AddTaskForm/AddTaskCard";
import { switchTasksSphere, updateTasks } from "../common/helpers";
import ExpandableAddTaskButton from "../common/ExpandableAddTaskButton";
import { transitions } from "../../../../common/helpers";
import EmptyTasks from "../common/EmptyTasks";

const SelectedWeekPage = ({
  year,
  week,
  monthData,
  sphere,
  healthTasks,
  learningTasks,
  mentalTasks,
  relationshipTasks,
  affairsTasks,
  weekNumber,
  t,
}) => {
  const history = useHistory();
  const { pathname } = useLocation();

  const currentSphere = switchTasksSphere(
    sphere,
    healthTasks.week,
    learningTasks.week,
    mentalTasks.week,
    relationshipTasks.week,
    affairsTasks.week
  );

  const generalDate = `${monthData.month} ${year} ${weekNumber} Неделя`;

  const [isTaskFormOpen, setTaskFormOpen] = useState(false);
  const [currentTasks, setCurrentTasks] = useState(
    currentSphere[generalDate] ? currentSphere[generalDate] : []
  );
  const [taskToEdit, setTaskToEdit] = useState(undefined);

  if (R.isEmpty(week)) {
    history.push(`${pathname}`);
  }

  const inputTask = () => {
    setTaskFormOpen(true);
  };

  useEffect(() => {
    updateTasks(setCurrentTasks, currentSphere, generalDate);
  }, [
    healthTasks,
    learningTasks,
    mentalTasks,
    relationshipTasks,
    affairsTasks,
    currentSphere,
    generalDate,
    taskToEdit,
  ]);

  const isMonthActual = monthData.count >= moment().format("MM");

  const weekTitle = t("time", { returnObjects: true }).week;
  const pageTitle = R.prop(sphere, t("sphere", { returnObjects: true }));

  return (
    <>
      <Sticky>
        <Col span={24}>
          <PageTitle>{pageTitle}</PageTitle>
        </Col>
        <CenteredRow>
          <Col offset={1} span={11}>
            {isMonthActual && (
              <ExpandableAddTaskButton
                isTaskFormOpen={isTaskFormOpen}
                inputTask={inputTask}
              />
            )}
          </Col>
          <ColWithRightTextAlign span={3}>
            <TasksSummaryInIcons
              tasksLength={currentTasks ? currentTasks.length : 0}
              subtasksLength={0}
              doneTasksLength={0}
            />
          </ColWithRightTextAlign>
          <ColWithRightTextAlign span={9}>
            <BlackText>
              {monthData.month} {year}
            </BlackText>
          </ColWithRightTextAlign>
        </CenteredRow>
        <ColWithRightTextAlign span={24}>
          <LittleBlackText>
            {weekNumber} {weekTitle}
          </LittleBlackText>
        </ColWithRightTextAlign>
      </Sticky>

      <OverflowContent offset={1} span={23} style={{ marginTop: "5vh" }}>
        <Expand
          open={isTaskFormOpen || Boolean(taskToEdit)}
          duration={300}
          transitions={transitions}
        >
          <AddTaskCard
            setvisible={setTaskFormOpen}
            sphere={sphere}
            period="week"
            week={generalDate}
            taskToEdit={taskToEdit}
            setTaskToEdit={setTaskToEdit}
          />
        </Expand>
        {R.isEmpty(currentTasks) && !isTaskFormOpen && !Boolean(taskToEdit) ? (
          <EmptyTasks />
        ) : (
          currentTasks.map(
            (task) =>
              (taskToEdit && taskToEdit.id) !== task.id && (
                <CardWithBorderRadius
                  key={uuid()}
                  onClick={() =>
                    updateTasks(setCurrentTasks, currentSphere, generalDate)
                  }
                >
                  <TaskCard
                    task={task}
                    sphere={sphere}
                    week={generalDate}
                    taskToEditId={taskToEdit ? taskToEdit.id : ""}
                    setTaskToEdit={setTaskToEdit}
                  />
                </CardWithBorderRadius>
              )
          )
        )}
      </OverflowContent>
    </>
  );
};

const mapStateToProps = (state) => ({
  healthTasks: state.healthTasks,
  learningTasks: state.learningTasks,
  mentalTasks: state.mentalTasks,
  relationshipTasks: state.relationshipTasks,
  affairsTasks: state.affairsTasks,
});

export default withNamespaces()(connect(mapStateToProps)(SelectedWeekPage));
