import React from "react";
import { List } from "antd";
import { useHistory, useLocation } from "react-router-dom";
import styled from "styled-components";
import moment from "moment";
import "moment/locale/ru";
import "moment/locale/uk";
import "moment/locale/en-gb";
import { withNamespaces } from "react-i18next";

import MonthsSidebarCalendar from "./MonthsSidebarCalendar";
import { capitalize } from "../../../../../common/helpers";
import { setMonthByYear } from "../helpers";

const MonthsColumnWithCalendar = ({
  visibleMonth,
  setVisibleMonth,
  setWeek,
  week,
  day,
  setDay,
  setWeekNumber,
  t,
}) => {
  const history = useHistory();
  const { pathname } = useLocation();

  moment.locale(t("lang"));
  const months = [
    {
      count: "01",
      month: capitalize(moment("01", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("01", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 0),
    },
    {
      count: "02",
      month: capitalize(moment("02", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("02", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 1),
    },
    {
      count: "03",
      month: capitalize(moment("03", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("03", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 2),
    },
    {
      count: "04",
      month: capitalize(moment("04", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("04", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 3),
    },
    {
      count: "05",
      month: capitalize(moment("05", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("05", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 4),
    },
    {
      count: "06",
      month: capitalize(moment("06", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("06", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 5),
    },
    {
      count: "07",
      month: capitalize(moment("07", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("07", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 6),
    },
    {
      count: "08",
      month: capitalize(moment("08", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("08", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 7),
    },
    {
      count: "09",
      month: capitalize(moment("09", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("09", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 8),
    },
    {
      count: "10",
      month: capitalize(moment("10", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("10", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 9),
    },
    {
      count: "11",
      month: capitalize(moment("11", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("11", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 10),
    },
    {
      count: "12",
      month: capitalize(moment("12", "MM").format("MMMM")),
      monthInGenetiveCase: capitalize(
        moment("12", "MM").format("D MMMM").replace(/\d+ /g, "")
      ),
      monthForCalendar: setMonthByYear(2020, 11),
    },
  ];

  const handleMonthSelect = (month, e) => {
    setVisibleMonth(month);
    if (e.target.nodeName === "SPAN") {
      history.push(`${pathname}?period=month`);
    }
  };

  return (
    <MonthsList
      dataSource={months}
      renderItem={(item) => (
        <ListItem
          onClick={(e) => handleMonthSelect(item, e)}
          selected={visibleMonth.month === item.month}
        >
          <span>{item.month}</span>
          {item.month === visibleMonth.month && (
            <MonthsSidebarCalendar
              week={week}
              day={day}
              monthData={visibleMonth}
              setWeek={setWeek}
              setDay={setDay}
              setWeekNumber={setWeekNumber}
            />
          )}
        </ListItem>
      )}
    />
  );
};

const MonthsList = styled(List)`
  height: 65vh;
  padding-bottom: 30px;
  overflow-x: hidden;
  overflow-y: auto;
  ::-webkit-scrollbar {
    width: 0;
  }
  .ant-list-split,
  .ant-list-item {
    border-bottom: 1px solid transparent;
  }
  .ant-spin-container {
    padding-left: 7px;
  }
`;

const ListItem = styled(List.Item)`
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  :hover {
    border-top: 1px solid #d2d8dd;
    border-bottom: 1px solid #d2d8dd;
    span {
      color: #3b3b3b;
    }
  }
  span {
    font-weight: 300;
    font-size: 28px;
    line-height: 34px;
    color: #909fab;
    text-align: center;
    border: none;
  }
`;

export default withNamespaces()(MonthsColumnWithCalendar);
