import React from "react";
import { List, Card, Col } from "antd";
import styled from "styled-components";
import { withNamespaces } from "react-i18next";

import { ReactComponent as GlobalPlanet } from "../../../../../assets/globalPlanet.svg";
import { ReactComponent as GlobalIdea } from "../../../../../assets/globalIdea.svg";
import { ReactComponent as GlobalPeople } from "../../../../../assets/globalPeople.svg";
import {
  SubTitle,
  FullSizeRow,
  Column,
} from "../../../../../common/StyledComponents";

const InfoList = ({ t }) => {
  const {
    information,
    aboutUs,
    whatIsHealth,
    mockedTextHealth,
    mockedTextAbout,
    mockedTextIdea,
  } = t("enterSphere", { returnObjects: true });

  const mockedInfo = [
    {
      icon: <GlobalPlanet />,
      title: whatIsHealth,
      info: mockedTextHealth,
    },
    {
      icon: <GlobalIdea />,
      title: aboutUs,
      info: mockedTextIdea,
    },
    {
      icon: <GlobalPeople />,
      title: aboutUs,
      info: mockedTextAbout,
    },
    {
      icon: <GlobalIdea />,
      title: aboutUs,
      info: mockedTextIdea,
    },
  ];

  return (
    <>
      <Col offset={1}>
        <SubTitle>{information}</SubTitle>
      </Col>
      <RowList
        dataSource={mockedInfo}
        renderItem={(item) => (
          <Card>
            <FullSizeRow>
              <Column span={7} style={{ height: "100%" }}>
                {item.icon}
              </Column>
              <Col offset={1} span={16}>
                <Col>
                  <DarkTitleText>{item.title}</DarkTitleText>
                </Col>
                <Col>{item.info}</Col>
              </Col>
            </FullSizeRow>
          </Card>
        )}
      />
    </>
  );
};

const RowList = styled(List)`
  margin-right: 20px;
  .ant-list-items {
    padding: 10px;
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;
    ::-webkit-scrollbar {
      width: 0px;
    }
  }
  .ant-card {
    min-width: 400px;
    margin-right: 15px;
    box-shadow: 0px 1px 20px rgba(0, 0, 0, 0.08);
    border-radius: 20px;
  }
  .ant-card-body {
    display: flex;
  }
`;

const DarkTitleText = styled.p`
  font-size: 21px;
  letter-spacing: -0.5px;
  line-height: 24px;
  color: #191919;
`;

export default withNamespaces()(InfoList);
