import React, { useState } from "react";
import { List, Row, Col, Popover } from "antd";
import styled from "styled-components";
import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";

import { IconButton, ThinText, Column } from "../../../common/StyledComponents";
import { DeleteSubtaskActionCreator } from "../../../redux/actionCreators/TasksActionCreators";
import PopoverMenu from "../../../components/PopoverMenu/PopoverMenu";
import { currentDayPointsFormat } from "../../../common/helpers";
import { ReactComponent as EmptyIcon } from "../../../assets/emptyHead.svg";

const CoverPlanList = ({
  list,
  period,
  taskVisible,
  deleteSubtask,
  exactDate,
  updateSubtasks,
  t,
  ...props
}) => {
  const [activeSubtasksList, setActiveSubtasksList] = useState(undefined);
  const [activeSubtask, setActiveSubtask] = useState(undefined);
  const [popoverVisibleId, setPopoverVisibleId] = useState(undefined);

  const hidePopover = () => {
    setPopoverVisibleId(undefined);
  };
  const {
    dayEmptyText,
    weekEmptyText,
    monthEmptyText,
    yearEmptyText,
  } = t("home", { returnObjects: true });

  let taskTextWidth;
  let emptyText = "";
  switch (period) {
    case "day":
      taskTextWidth = 20;
      emptyText = dayEmptyText;
      break;
    case "month":
      taskTextWidth = 18;
      emptyText = monthEmptyText;
      break;
    case "year":
      taskTextWidth = 18;
      emptyText = yearEmptyText;
      break;
    case "week":
      taskTextWidth = 19;
      emptyText = weekEmptyText;
      break;
    default:
      break;
  }

  const subtaskDropdown = (
    <PopoverMenu
      activeSubtask={activeSubtask}
      updateSubtasks={updateSubtasks}
      period={period}
      exactDate={exactDate}
      hidePopover={hidePopover}
    />
  );

  const handleTaskClose = (e) => {
    const parents = [];
    let element = e.target;
    while (element.parentNode) {
      parents.unshift(element.parentNode);
      element = element.parentNode;
    }
    if (activeSubtasksList && !parents.includes(activeSubtasksList)) {
      props.setTaskVisible && props.setTaskVisible(undefined);
      hidePopover();
    }
  };
  document.addEventListener("click", handleTaskClose);

  const handleSubtaskClick = (e) => {
    if (list.length > 0) {
      props.setTaskVisible && props.setTaskVisible(true);
      setActiveSubtasksList(e.currentTarget);
    }
  };

  return (
    <>
      {Boolean(list.length) ? (
        <ListWithTime
          size="small"
          onClick={handleSubtaskClick}
          dataSource={list}
          style={{marginTop: period==="day" ? 40 : 0}} 
          opacity={period === "day" || taskVisible ? 1 : 0}
          renderItem={(item, index) => (
            <Popover
              visible={item.id === popoverVisibleId}
              content={subtaskDropdown}
              trigger="click"
              placement="bottomRight"
              onVisibleChange={() => setPopoverVisibleId(item.id)}
            >
              <ListItem
                iconopacity={item.id === popoverVisibleId ? 1 : 0}
                onClick={() => setActiveSubtask(item)}
              >
                <Row>
                  {period === "day" && (
                    <Col span={3} style={{ padding: "0 5px", minWidth: "fit-content" }}>
                      {item.deadline === currentDayPointsFormat ? (
                        <ColoredTextInBox>Deadline</ColoredTextInBox>
                      ) : (
                        (item.time !==
                          (list[index - 1] && list[index - 1].time) ||
                          (list[index - 1] &&
                            list[index - 1].deadline ===
                              currentDayPointsFormat)) && (
                          <ColoredTextInBox>{item.time}</ColoredTextInBox>
                        )
                      )}
                    </Col>
                  )}
                  {(period === "month" || period === "year") && (
                    <Col span={5}>
                      <ColoredTextInBox>
                        {item.date || "22.02"}
                      </ColoredTextInBox>
                    </Col>
                  )}
                  {period === "week" && (
                    <>
                      <Col span={2}>
                        <ColoredTextInBox>ПН</ColoredTextInBox>
                      </Col>
                      <Col span={2}>{item.date}</Col>
                    </>
                  )}
                  <Col offset={1} span={taskTextWidth}>
                    <ColoredThinText
                      style={{
                        color:
                          item.deadline === currentDayPointsFormat &&
                          period === "day"
                            ? "#F85C71"
                            : "#fff",
                      }}
                    >
                      {item.plan || item.title}
                    </ColoredThinText>
                  </Col>
                </Row>

                <IconButton type="more" />
              </ListItem>
            </Popover>
          )}
        />
      ) : (
        <AbsoluteColumn period={period}>
          <ResponsiveEmptyIcon period={period} />
          <TextWithBefore period={period}>{emptyText}</TextWithBefore>
        </AbsoluteColumn>
      )}
    </>
  );
};

const AbsoluteColumn = styled(Column)`
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: ${(prop) => (prop.period === "day" ? "0" : "0 40px")};
`;

const ResponsiveEmptyIcon = styled(EmptyIcon)`
  opacity: ${(prop) => (prop.period === "day" ? 1 : 0)};
  height: ${(prop) => (prop.period === "day" ? "35%" : "25%")};
  min-height: 40px;
  max-width: ${(prop) => (prop.period === "day" ? "30%" : "50%")};
`;

const TextWithBefore = styled.p`
  opacity: ${(prop) => (prop.period === "day" ? 1 : 0)};
  font-size: 18px;
  line-height: 22px;
  margin: ${(prop) => (prop.period === "day" ? "30px" : "0")};
  font-weight: 300;
  color: #ffffff;
  ::before {
    margin: ${(prop) =>
      prop.period === "day" ? "10px auto 30px auto" : "20px auto"};
    content: "";
    display: block;
    width: 35%;
    min-width: 150px;
    height: 3px;
    background: #f85c71;
  }
`;

const ListItem = styled(List.Item)`
  .anticon {
    opacity: ${(prop) => prop.iconopacity};
  }
  :hover .anticon {
    opacity: 1;
  }
`;

const ColoredThinText = styled(ThinText)`
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  color: #fff;
`;

const ColoredTextInBox = styled.p`
  border: 1px solid #f85c71;
  box-sizing: border-box;
  border-radius: 50px;
  font-size: 12px;
  line-height: 15px;
  color: #ffffff;
  text-align: center;
  padding: 5px 0;
  margin: 0;
`;

const ListWithTime = styled(List)`
  ul,
  .ant-row {
    width: 100%;
  }
  ul {
    opacity: ${(props) => props.opacity};
    font-size: 22px;
    line-height: 25px;
    color: #eee;
  }
  ::-webkit-scrollbar {
    width: 0px;
  }
  .ant-list-item,
  .ant-list-split,
  .ant-list-item:last-child {
    border: none;
    padding: 10px 0;
    border-bottom: 1px solid transparent;
  }
  .ant-list-item:hover {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
`;

const mapDispatchToProps = (dispatch) => ({
  deleteSubtask: (sphere, subtask, period, exactDate) =>
    dispatch(DeleteSubtaskActionCreator(sphere, subtask, period, exactDate)),
});

export default withNamespaces()(
  connect(null, mapDispatchToProps)(CoverPlanList)
);
