import React, { useState } from "react";
import { Col, Row, Form, Switch } from "antd";
import styled from "styled-components";
import { v4 as uuid } from "uuid";
import { withNamespaces } from "react-i18next";

import {
  TaskInput,
  AddTaskTitle,
} from "../../../../../../common/StyledComponents";
import SubtasksList from "../../SubtasksList";
import { ReactComponent as AddIcon } from "../../../../../../assets/add.svg";
import { ReactComponent as EverydayTaskIcon } from "../../../../../../assets/everyDay.svg";

const SubtaskForm = ({
  sphere,
  validateFields,
  setSubtasks,
  subtasks,
  getFieldDecorator,
  t,
}) => {
  const [isEverydaySubtask, setEverydaySubtask] = useState(false);
  const handleSubmit = (e) => {
    e.preventDefault();
    validateFields((err, values) => {
      if (!err) {
        const newSubtask = {
          id: uuid(),
          plan: values.subtask,
          date: "21.02.20",
          time: "09:00",
          isEveryday: isEverydaySubtask,
          sphere,
        };
        setSubtasks([newSubtask, ...subtasks]);
      }
    });
  };

  const { addSubtask, needToDo, everydayTask } = t("aim", { returnObjects: true });

  return (
    <Col offset={1} span={22} style={{ marginTop: 10, marginBottom: 20 }}>
      <AddTaskTitle>{addSubtask}</AddTaskTitle>
      <Form onSubmit={handleSubmit} style={{ marginTop: 30 }}>
        <Row>
          <Col span={15}>
            <Form.Item>
              {getFieldDecorator("subtask")(
                <AddTaskInput
                  placeholder={needToDo}
                  prefix={<AddIcon />}
                />
              )}
            </Form.Item>
          </Col>
          <Col offset={1} span={8}>
            <Form.Item>
              <SwitchWrapper>
                <Switch onChange={setEverydaySubtask} />
                <SwitchText>{everydayTask}</SwitchText>
                <EverydayTaskIcon style={{ marginLeft: 30 }} />
              </SwitchWrapper>
            </Form.Item>
          </Col>
        </Row>
      </Form>
      <SubtasksList listToRender={subtasks} />
    </Col>
  );
};

const AddTaskInput = styled(TaskInput)`
  margin-bottom: 0px;
`;

const SwitchWrapper = styled.div`
  .ant-switch {
    height: 17px;
  }
  .ant-switch::after {
    height: 12px;
    width: 12px;
  }
  .ant-switch-checked {
    background-color: #f85c71;
  }
`;

const SwitchText = styled.span`
  padding-left: 20px;
  font-size: 22px;
  line-height: 25px;
  color: #bebdbd;
  font-weight: 300;
`;

export default withNamespaces()(SubtaskForm);
