import React from "react";
import { Select } from "antd";
import { withNamespaces } from "react-i18next";
import * as R from "ramda";
import { connect } from "react-redux";

import i18n from "../../translations/i18n";
import { setLanguageActionCreator } from "../../redux/actionCreators/LanguageActionCreators";

const { Option } = Select;

const Settings = ({ t, setLanguage }) => {
  const changeLanguage = (lng) => {
    i18n.changeLanguage(lng);
  };
  const currentLanguageTitle = R.prop(
    "currentLanguage",
    t("settings", { returnObjects: true })
  );
  const ukrainian = R.prop("ukrainian", t("settings", { returnObjects: true }));
  const russian = R.prop("russian", t("settings", { returnObjects: true }));
  const english = R.prop("english", t("settings", { returnObjects: true }));

  const handleChange = (lang) => {
    changeLanguage(lang);
    setLanguage(lang)
  };

  return (
    <>
      <h1>{currentLanguageTitle}</h1>
      <Select
        defaultValue={t("lang")}
        style={{ width: 120 }}
        onChange={handleChange}
      >
        <Option value="ru">{russian}</Option>
        <Option value="en-gb">{english}</Option>
        <Option value="uk">{ukrainian}</Option>
      </Select>
    </>
  );
};

const mapDispatchToProps = (dispatch) => ({
  setLanguage: (language) => dispatch(setLanguageActionCreator(language)),
});

export default withNamespaces()(connect(null, mapDispatchToProps)(Settings));
