import React from "react";
import styled from "styled-components";
import { Menu, Layout } from "antd";
import { NavLink } from "react-router-dom";
import { withNamespaces } from "react-i18next";

import { ReactComponent as DiaryIcon } from "../../assets/diary.svg";
import { ReactComponent as CharityIcon } from "../../assets/worldwide.svg";
import { ReactComponent as NewsIcon } from "../../assets/newspaper.svg";

const { Footer } = Layout;

const PageFoooter = ({ t }) => {
  const { diary, charity, news } = t("footer", { returnObjects: true });

  return (
    <MainFooter>
      <FooterMenu mode="horizontal">
        <FooterMenuItem key="mail">
          <NavLink to="/charts">
            <DiaryIcon style={{ width: 30 }} />
            <FooterText>{diary}</FooterText>
          </NavLink>
        </FooterMenuItem>
        <FooterMenuItem key="user">
          <CharityIcon style={{ width: 30 }} />
          <FooterText>{charity}</FooterText>
        </FooterMenuItem>
        <FooterMenuItem key="settings">
          <NewsIcon style={{ width: 30 }} />
          <FooterText>{news}</FooterText>
        </FooterMenuItem>
      </FooterMenu>
    </MainFooter>
  );
};

const MainFooter = styled(Footer)`
  width: 100%;
  display: flex;
  justify-content: center;
  background: #fff;
  min-height: max-content;
  height: 12vh;
  padding: 8px 0;
  z-index: 3;
  overflow: hidden;
`;

const FooterMenu = styled(Menu)`
  border: none;
`;

const FooterMenuItem = styled(Menu.Item)`
  text-align: center;
`;

const FooterText = styled.p`
  margin-bottom: 10px;
  font-weight: 300;
  font-size: 14px;
  letter-spacing: -0.5px;
  line-height: 14px;
  color: #1d4e89;
`;

export default withNamespaces()(PageFoooter);
