import React from "react";
import { Form, Input } from "antd";
import styled from "styled-components";
import { withNamespaces } from "react-i18next";

const DayPlanForm = ({ form, t }) => {
  const handleSubmit = (e) => {
    e.preventDefault();
    form.validateFields((err, values) => {
      if (!err) {
        console.log("Received values of form: ", values);
      }
    });
  };
  const { getFieldDecorator } = form;

  const { dayThought, dayThanks } = t("home", { returnObjects: true });

  return (
    <DayForm onSubmit={handleSubmit}>
      <FormItem>
        {getFieldDecorator("thoughtOfDay")(<Input placeholder={dayThought} />)}
      </FormItem>
      <FormItem>
        {getFieldDecorator("thanksOfDay")(
          <Input placeholder={dayThanks} />
        )}
      </FormItem>
    </DayForm>
  );
};

const DayForm = styled(Form)`
  input {
    background: transparent;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    font-size: 16px;
    line-height: 20px;
    color: #fff;
  }
  .ant-form-item-control {
    line-height: 25px;
  }
`;

const FormItem = styled(Form.Item)`
  .ant-form-item-children {
    margin-left: 0;
  }
  .ant-input:focus {
    border-bottom: 1px solid #f85c71;
    outline: none;
    box-shadow: none;
  }
`;

const WrappedDayPlanForm = Form.create({ name: "dayplan_form" })(DayPlanForm);

export default withNamespaces()(WrappedDayPlanForm);
