import React, { useState, useEffect, useCallback } from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";

import YearShedule from "../../../assets/slide-4.png";
import RetinaYearShedule from "../../../assets/slide-4@2x.png";
import {
  PlanCover,
  ResponsiveImage,
  PurposeList,
} from "../../../common/StyledComponents";
import CoverPlanList from "./CoverPlanList";
import PeriodHeader from "./PeriodHeader";
import {
  getTasksorSubtasksForDifferentPeriods,
  currentYear,
} from "../../../common/helpers";

const YearPlan = ({
  healthTasks,
  learningTasks,
  mentalTasks,
  relationshipTasks,
  affairsTasks,
  t,
}) => {
  const [taskVisible, setTaskVisible] = useState(undefined);
  const [list, setList] = useState([]);

  const updateSubtasks = useCallback(() => {
    setList(
      getTasksorSubtasksForDifferentPeriods(
        healthTasks,
        learningTasks,
        mentalTasks,
        relationshipTasks,
        affairsTasks,
        currentYear
      )
    );
  }, [
    healthTasks,
    learningTasks,
    mentalTasks,
    relationshipTasks,
    affairsTasks,
  ]);

  useEffect(() => {
    updateSubtasks();
  }, [updateSubtasks]);

  const { year } = t("time", { returnObjects: true });

  return (
    <PlanCover hovering={taskVisible ? String(taskVisible) : undefined}>
      <div className="planCover">
        <PurposeList>
          <PeriodHeader period={year} tasksLength={list.length} />
          <CoverPlanList
            period="year"
            list={list}
            taskVisible={taskVisible}
            setTaskVisible={setTaskVisible}
            exactDate={currentYear}
            updateSubtasks={updateSubtasks}
          />
        </PurposeList>
      </div>

      <YearImage
        src={YearShedule}
        srcSet={`${YearShedule},
            ${RetinaYearShedule} 2x`}
        alt="logo"
      />
    </PlanCover>
  );
};

const YearImage = styled(ResponsiveImage)`
  height: calc(31vh - 10px);
  border-radius: 10px;
`;

const mapStateToProps = (state) => ({
  healthTasks: state.healthTasks.year,
  learningTasks: state.learningTasks.year,
  mentalTasks: state.mentalTasks.year,
  relationshipTasks: state.relationshipTasks.year,
  affairsTasks: state.affairsTasks.year,
});

export default withNamespaces()(connect(mapStateToProps)(YearPlan));
