import React, { useState, useEffect, useCallback } from "react";
import { Row, Col } from "antd";
import styled from "styled-components";
import { connect } from "react-redux";
import moment from "moment";
import "moment/locale/ru";
import "moment/locale/uk";
import "moment/locale/en-gb";
import { withNamespaces } from "react-i18next";

import { ResponsiveImage } from "../../../common/StyledComponents";
import DayShedule from "../../../assets/slide-1.png";
import RetinaDayShedule from "../../../assets/slide-1@2x.png";
import DayPlanForm from "./DayPlanForm";
import ProgressLine from "./ColoredProgress";
import PlanList from "./CoverPlanList";
import {
  getTasksorSubtasksForDifferentPeriods,
  today,
  capitalize,
  getAllTasksFromPeriod,
} from "../../../common/helpers";
import TasksSummaryInIcons from "../../../components/TasksLengthRow/TasksSummaryInIcons";

const DayPlan = ({
  healthTasks,
  learningTasks,
  mentalTasks,
  relationshipTasks,
  affairsTasks,
  t,
}) => {
  moment.locale(t("lang"));
  const [subtasksList, setSubtasksList] = useState([]);
  const [tasksLength, setTasksLength] = useState(0);

  const updateSubtasks = useCallback(() => {
    setSubtasksList(
      getTasksorSubtasksForDifferentPeriods(
        healthTasks,
        learningTasks,
        mentalTasks,
        relationshipTasks,
        affairsTasks,
        today
      ).sort((a, b) => a.time.localeCompare(b.time))
    );
    setTasksLength(
      getAllTasksFromPeriod(
        healthTasks,
        learningTasks,
        mentalTasks,
        relationshipTasks,
        affairsTasks,
        today
      ).length
    );
  }, [
    healthTasks,
    learningTasks,
    mentalTasks,
    relationshipTasks,
    affairsTasks,
  ]);

  useEffect(() => {
    updateSubtasks();
  }, [updateSubtasks]);

  const dayNumber = moment().format("D");
  const currentWeekDay = capitalize(moment().format("dddd"));
  const currentMonthInGenetiveCase = capitalize(
    moment().format("D MMMM").replace(/\d+ /g, "")
  );

  return (
    <Plan>
      <PurposeList>
        <Column>
          <MainContent>
            <Col span={18}>
              <DayNumber span={5}>{dayNumber}</DayNumber>
              <Col span={19}>
                <Month span={24}>{currentMonthInGenetiveCase}</Month>
                <Day span={24}>{currentWeekDay}</Day>
              </Col>
            </Col>
            <Col span={6}>
              <TasksSummaryInIcons
                tasksLength={tasksLength}
                subtasksLength={subtasksList.length}
                doneTasksLength={0}
              />
            </Col>
            <OverflowListCol
              hasheight={Boolean(subtasksList.length) ? "" : "45vh"}
              offset={1}
              span={22}
            >
              <PlanList
                period="day"
                list={subtasksList}
                taskVisible={true}
                exactDate={today}
                updateSubtasks={updateSubtasks}
              />
            </OverflowListCol>
          </MainContent>
          {Boolean(subtasksList.length) && (
            <FooterCol span={24}>
              <DayPlanForm />
              <ProgressLine />
            </FooterCol>
          )}
        </Column>
      </PurposeList>

      <DayImage
        src={DayShedule}
        srcSet={`${DayShedule},
            ${RetinaDayShedule} 2x`}
        alt="logo"
      />
    </Plan>
  );
};

const Column = styled.div`
  overflow: auto;
  ::-webkit-scrollbar {
    width: 0px;
  }
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
`;

const MainContent = styled.div`
  flex: 1 0 auto;
`;

const FooterCol = styled(Col)`
  flex-shrink: 0;
  padding: 30px 0;
`;

const OverflowListCol = styled(Col)`
  height: ${(prop) => prop.hasheight};
  // overflow: auto;
  ::-webkit-scrollbar {
    width: 0px;
  }
`;

const DayImage = styled(ResponsiveImage)`
  height: calc(60vh - 10px);
  border-radius: 10px;
`;

const Month = styled(Col)`
  font-size: 28px;
  line-height: 37px;
  color: #ffffff;
`;

const Day = styled(Col)`
  font-size: 20px;
  line-height: 24px;
  color: #ffffff;
`;

const DayNumber = styled(Col)`
  text-align: center;
  padding-top: 5px;
  font-weight: 600;
  font-size: 44px;
  line-height: 54px;
  color: #ffffff;
`;

const PurposeList = styled(Row)`
  position: absolute;
  width: 100%;
  color: #fff;
  border-radius: 10px;
  background: rgba(26, 26, 26, 0.9);
  padding: 40px 40px 0 40px;
  font-size: 22px;
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  .ant-empty-description {
    color: #fff;
  }
  .ant-typography {
    color: #fff;
  }
`;

const Plan = styled.div`
  cursor: pointer;
  height: 100%;
`;

const mapStateToProps = (state) => ({
  healthTasks: state.healthTasks.day,
  learningTasks: state.learningTasks.day,
  mentalTasks: state.mentalTasks.day,
  relationshipTasks: state.relationshipTasks.day,
  affairsTasks: state.affairsTasks.day,
});

export default withNamespaces()(connect(mapStateToProps)(DayPlan));
