import React, { useState } from "react";
import { Row, Col, Menu, List } from "antd";
import styled from "styled-components";
import { withNamespaces } from "react-i18next";

import { PageTitle, ThinText } from "../../common/StyledComponents";
import AxisChart from "../../common/AxisChart";
import Pie from "./components/ProcessPie";
import ResultsList from "./components/ResultsList";
import TasksList from "./components/TasksList";

const ChartsPage = ({ t }) => {
  const [isResultsListVisible, setResultsListVisible] = useState(false);
  const [isTasksListVisible, setTasksListVisible] = useState(false);

  const { week, month, year, today, halfYear } = t("time", {
    returnObjects: true,
  });
  const {
    main,
    progress,
    result,
    achievements,
    thanks,
    thoughts,
    doneTasks,
    done,
    inProgress,
    notDone,
  } = t("diary", { returnObjects: true });

  const pusposeStatus = [
    { status: done, color: "#1D4E89" },
    { status: inProgress, color: "#F85C71" },
    { status: notDone, color: "#3B3B3B" },
  ];

  return (
    <Row>
      <Col offset={1} span={23}>
        <PageTitle>River</PageTitle>
      </Col>
      <Col offset={9} span={15}>
        <AxisChartMenu mode="horizontal" selectedKeys={["main"]}>
          <Menu.Item key="main">{main}</Menu.Item>
          <Menu.Item key="progress">{progress}</Menu.Item>
          <Menu.Item key="result">{result}</Menu.Item>
          <Menu.Item key="achievements">{achievements}</Menu.Item>
          <Menu.Item key="thanks">{thanks}</Menu.Item>
          <Menu.Item key="thoughts">{thoughts}</Menu.Item>
        </AxisChartMenu>
      </Col>
      <Col span={13}>
        {isTasksListVisible ? (
          <TasksList />
        ) : (
          <>
            <PieCol span={24}>
              <Col span={17}>
                <Pie openTasksList={() => setTasksListVisible(true)} />
              </Col>
              <List
                grid={{ gutter: 16, column: 1 }}
                dataSource={pusposeStatus}
                renderItem={(item) => (
                  <PieStatusItem beforecolor={item.color}>
                    <ThinText>{item.status}</ThinText>
                  </PieStatusItem>
                )}
              />
            </PieCol>
            <Col offset={4} span={20}>
              <RedThinText>{doneTasks}</RedThinText>
              <br />
              <ThinText>34/ 78%</ThinText>
            </Col>
          </>
        )}
      </Col>
      <Col span={11}>
        <AxisChartCol offset={1} span={23}>
          {isResultsListVisible ? (
            <ResultsList />
          ) : (
            <MarginAxisChart
              openResultsList={() => setResultsListVisible(true)}
            />
          )}
          <AxisChartMenu
            mode="horizontal"
            selectedKeys={["halfYear"]}
            style={{ margin: "35px 35px 45px 0" }}
          >
            <Menu.Item key="today">{today}</Menu.Item>
            <Menu.Item key="week">{week}</Menu.Item>
            <Menu.Item key="month">{month}</Menu.Item>
            <Menu.Item key="halfYear">{halfYear}</Menu.Item>
            <Menu.Item key="year">{year}</Menu.Item>
          </AxisChartMenu>
        </AxisChartCol>
      </Col>
    </Row>
  );
};

const MarginAxisChart = styled(AxisChart)`
  svg {
    margin: 100px;
  }
`;

const PieCol = styled(Col)`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const RedThinText = styled(ThinText)`
  color: #f85c71;
`;

const PieStatusItem = styled(List.Item)`
  :before {
    content: "";
    display: inline-block;
    width: 26px;
    height: 26px;
    background: ${(props) => props.beforecolor};
    margin-right: 20px;
  }
`;

const AxisChartCol = styled(Col)`
  text-align: right;
`;

const AxisChartMenu = styled(Menu)`
  border: none;
  .ant-menu-item {
    font-size: 22px;
    letter-spacing: 2.38px;
    font-weight: 300;
    color: #b5b5b5;
  }
  .ant-menu-item-selected,
  ant-menu-item:hover {
    color: #f85c71;
    border-bottom: 2px solid transparent;
  }
`;

export default withNamespaces()(ChartsPage);
