import { SPHERES } from "../../../../common/helpers";

export const switchTasksSphere = (
  sphere,
  healthTasks,
  learningTasks,
  mentalTasks,
  relationshipTasks,
  affairsTasks
) => {
  // console.log(sphere, affairsTasks, SPHERES.affairs, healthTasks, learningTasks, mentalTasks, relationshipTasks);
  switch (sphere) {
    case SPHERES.health:
      return healthTasks || [];
    case SPHERES.learning:
      return learningTasks || [];
    case SPHERES.mental:
      return mentalTasks || [];
    case SPHERES.relationship:
      return relationshipTasks || [];
    case SPHERES.affairs:
      return affairsTasks || [];
    default:
      return [];
  }
};

export const setMonthByYear = (year, monthCount) => {
  return new Date(year, monthCount);
};


