import types from "../actionTypes";

export const AddTaskActionCreator = (sphere, task, period, date) => ({
  type: types.ADD_NEW_TASK,
  payload: { sphere, period, task, date },
});

export const AddSubtaskActionCreator = (
  sphere,
  task,
  subtask,
  period,
  exactDate
) => ({
  type: types.ADD_NEW_SUBTASK,
  payload: { sphere, task, subtask, period, exactDate },
});

export const DeleteTaskActionCreator = (sphere, period, date, id) => ({
  type: types.DELETE_TASK,
  payload: { sphere, period, date, id },
});

export const DeleteSubtaskActionCreator = (
  sphere,
  subtask,
  period,
  exactDate
) => ({
  type: types.DELETE_SUBTASK,
  payload: { sphere, subtask, period, exactDate },
});

export const EditTaskActionCreator = (sphere, task, period, date) => ({
  type: types.EDIT_TASK,
  payload: {sphere, task, period, date},
});