import React from "react";
import { List, Row, Col } from "antd";
import styled from "styled-components";
import { withNamespaces } from "react-i18next";

import { ThinText, SimpleButton } from "../../../common/StyledComponents";

const mockedData = [
  {
    text:
      "На этой неделе чувствовал себя слабо, мало двигался в результате не очень доволен собой",
    amountOfTasks: "2",
    date: "20.02.2019",
  },
  {
    text: "Неделя была продуктивной",
    amountOfTasks: "5",
    date: "20.02.2019",
  },
  {
    text: "Завершены главные цели года",
    amountOfTasks: "9",
    date: "20.02.2019",
  },
  {
    text: "Открыл для себя новые возможности",
    amountOfTasks: "7",
    date: "20.02.2019",
  },
  {
    text: "Неделя пройдена спокойно, поддерживал форму, решал задачи",
    amountOfTasks: "5",
    date: "20.02.2019",
  },
];

const TasksList = ({ t }) => {
  const { more } = t("diary", { returnObjects: true });

  return (
    <Row>
      <Col offset={2} span={20}>
        <BorderedList
          grid={{ gutter: 16, column: 1 }}
          dataSource={mockedData}
          renderItem={(item) => (
            <List.Item>
              <ItemRow>
                <TasksAmont span={1}>{item.amountOfTasks}</TasksAmont>
                <Col span={17}>
                  <ThinText>{item.text}</ThinText>
                </Col>
                <Col span={4} offset={2}>
                  <ThinText>20.02.2019</ThinText>
                </Col>
              </ItemRow>
            </List.Item>
          )}
        />
      </Col>
      <Col span={3} offset={11}>
        <SimpleButton>
          <ThinText>{more}</ThinText>
        </SimpleButton>
      </Col>
    </Row>
  );
};

const TasksAmont = styled(Col)`
  font-size: 27px;
  font-weight: 700;
  letter-spacing: 2.62px;
  color: #3b3b3b;
`;

const ItemRow = styled(Row)`
  display: flex;
  align-items: center;
`;

const BorderedList = styled(List)`
  margin-top: 50px;
  text-align: left;
  .ant-col .ant-list-item {
    border-bottom: 1px solid #b5b5b5;
    margin-top: 30px;
  }
`;

export default withNamespaces()(TasksList);
