import React from "react";
import styled from "styled-components";
import { Col } from "antd";
import { withNamespaces } from "react-i18next";

import { CancelButton } from "../../../../common/StyledComponents";
import { ReactComponent as AchievementsIcon } from "../../../../assets/achievements.svg";

const Achievements = ({ setAchievementsVisible, t }) => {
  const { shareAchievements, achievements, shareAchievementsSubtitle } = t(
    "profile",
    {
      returnObjects: true,
    }
  );
  return (
    <>
      <Col offset={1} span={6} style={{ minWidth: "fit-content" }}>
        <AchievementsIcon style={{ height: 170 }} />
      </Col>
      <Col offset={1} span={16}>
        <BigText>{shareAchievements}</BigText>
        <BigSubtext>{shareAchievementsSubtitle}</BigSubtext>
        <CancelButton onClick={() => setAchievementsVisible(true)}>
          {achievements}
        </CancelButton>
      </Col>
    </>
  );
};

const BigText = styled.p`
  font-size: 35px;
  color: #3b3b3b;
  margin-bottom: 0px;
  line-height: 38px;
`;

const BigSubtext = styled.p`
  font-size: 20px;
  letter-spacing: -0.5px;
  color: #3b3b3b;
`;

export default withNamespaces()(Achievements);
