import React, { useState } from "react";
import { Row, Col } from "antd";
import { connect } from "react-redux";

import { PageTitle } from "../../common/StyledComponents";
import UserCard from "./components/ProfileCard/UserCard";
import Mission from "./components/Mission/Mission";
import Values from "./components/Values/Values";
import Stats from "./components/Stats/Stats";
import AchievementsList from "./components/Achievements/AchievementsList";
import Achievements from "./components/Achievements/Achievements";
// import axiosInstance from "../../axios/axiosInstance";

const Profile = ({ user }) => {
  const [areAchievementsVisible, setAchievementsVisible] = useState(false);
console.log(user)
  return (
    <Row gutter={[16, 15]}>
      <Col offset={1} span={23}>
        <PageTitle>River</PageTitle>
      </Col>
      <Col span={7}>
        <UserCard
          firstName={
            user.first_name ||
            (user.name && user.name.split(" ")[0])
          }
          lastName={
            user.last_name || (user.name && user.name.split(" ")[1])
          }
          mindfullnessLevel={user.mindfulness}
          email={user.email}
          occupation={user.job}
        />
      </Col>
      <Col span={11}>
        <Mission />
      </Col>
      <Col span={5}>
        <Values />
      </Col>
      <Col span={18} style={{ marginTop: areAchievementsVisible ? 0 : 19 }}>
        <Row align="middle" type="flex">
          {areAchievementsVisible ? (
            <AchievementsList />
          ) : (
            <Achievements setAchievementsVisible={setAchievementsVisible} />
          )}
        </Row>
      </Col>
      <Col span={5}>
        <Stats />
      </Col>
    </Row>
  );
};

const mapStateToProps = (state) => ({
  user: state.auth,
});

export default connect(mapStateToProps)(Profile);
