import React from "react";
import { withNamespaces } from "react-i18next";

import { ProfileCard, ProfileCardTitle, ProfileCardText } from "../../../../common/StyledComponents";

const Mission = ({t}) => {
  const {mission, missionText} = t("profile", { returnObjects: true });
  return (
    <ProfileCard>
      <ProfileCardTitle>{mission}</ProfileCardTitle>
      <ProfileCardText>
        {missionText}
      </ProfileCardText>
    </ProfileCard>
  );
};

export default withNamespaces()(Mission);
