import React from "react";
import styled from "styled-components";
import { Col, List } from "antd";
import { withNamespaces } from "react-i18next";

import {
  ProfileCard,
  ProfileCardTitle,
  FullSizeRow,
} from "../../../../common/StyledComponents";
import { ReactComponent as PointIcon } from "../../../../assets/point.svg";

const Values = ({ t }) => {
  const { values, value1, value2, value3 } = t("profile", {
    returnObjects: true,
  });
  const valuesList = [{ text: value1 }, { text: value2 }, { text: value3 }];

  return (
    <ProfileCard>
      <ProfileCardTitle>{values}</ProfileCardTitle>
      <MarkedList
        dataSource={valuesList}
        renderItem={(item) => (
          <List.Item>
            <FullSizeRow>
              <Col span={1}>
                <PointIcon />
              </Col>
              <Col span={23}>{item.text}</Col>
            </FullSizeRow>
          </List.Item>
        )}
      />
    </ProfileCard>
  );
};

const MarkedList = styled(List)`
  .ant-list-item {
    border: none;
    padding: 0;
  }
  .ant-col-1 {
    padding: 0 !important;
  }
  .ant-col-23 {
    padding: 0px 0 15px 10px !important;
    font-weight: 300;
    font-size: 14px;
    line-height: 17px;
    color: #3b3b3b;
  }
`;

export default withNamespaces()(Values);
