import types from "../../actionTypes";
import { SPHERES } from "../../../common/helpers";
import {
  addNewTask,
  addNewSubtask,
  deleteTask,
  deleteSubtask,
  editTask,
} from "../helpers";

const initialState = localStorage.getItem("learningTasks")
  ? JSON.parse(localStorage.getItem("learningTasks"))
  : {
      year: {},
      month: {},
      week: {},
      day: {},
    };

const LearningTasksReducer = (state = initialState, { type, payload }) => {
  if (payload && payload.sphere === SPHERES.learning) {
    switch (type) {
      case types.ADD_NEW_TASK:
        return addNewTask(state, payload, "learningTasks");
      case types.ADD_NEW_SUBTASK:
        return addNewSubtask(state, payload, "learningTasks");
      case types.DELETE_TASK:
        return deleteTask(state, payload, "learningTasks");
      case types.DELETE_SUBTASK:
        return deleteSubtask(state, payload, "learningTasks");
      case types.EDIT_TASK:
        return editTask(state, payload, "learningTasks");
      default:
        return state;
    }
  } else {
    return state;
  }
};

export default LearningTasksReducer;
