import React, { useState } from "react";
import { Col, Row, Divider, Form, Dropdown, Menu } from "antd";
import styled from "styled-components";
import { connect } from "react-redux";
import { v4 as uuid } from "uuid";
import Expand from "react-expand-animated";
import { withNamespaces } from "react-i18next";

import {
  RegularCardText,
  MediumText,
  Column,
  TaskIconsWrapper,
  TaskInput,
  IconButton,
  TaskTitle,
  CardThinText,
} from "../../../../../common/StyledComponents";
import { ReactComponent as NotificationIcon } from "../../../../../assets/notification.svg";
import { ReactComponent as BiggerListIcon } from "../../../../../assets/list.svg";
import { ReactComponent as DollarIcon } from "../../../../../assets/dollar.svg";
import TaskVerticalProgress from "./TaskVerticalProgress";
import SubtasksList from "../SubtasksList";
import { ReactComponent as AddIcon } from "../../../../../assets/add.svg";
import {
  AddSubtaskActionCreator,
  DeleteTaskActionCreator,
} from "../../../../../redux/actionCreators/TasksActionCreators";
import { useQuery } from "../../../../../common/helpers";
import { switchTasksSphere } from "../helpers";
import ConfirmCancelModal from "./ConfirmCancelModal";
import DeadlineCalendar from "../../../../../components/Calendar/DeadlineCalendar";

const transitions = ["height", "opacity"];

const TaskCard = ({
  task,
  addSubtask,
  deleteTask,
  form,
  sphere,
  learningTasks,
  mentalTasks,
  affairsTasks,
  healthTasks,
  relationshipTasks,
  setTaskToEdit,
  t,
  ...props
}) => {
  const [areSubtasksOpen, setSubtasksOpen] = useState(false);
  const [confirmVisible, setConfirmVisible] = useState(false);
  const [isCalendarVisible, setCalendarVisible] = useState(false);

  const { validateFields, getFieldDecorator, resetFields } = form;
  const query = useQuery();
  const period = query.get("period");

  const exactDate = props[period];

  const handleDelete = async () => {
    await deleteTask(sphere, period, exactDate, task.id);
    props.updateDayTasks && props.updateDayTasks();
  };

  const { cancel } = t("auth", { returnObjects: true });
  const { edit, ready } = t("aim", { returnObjects: true });

  const cardDropdown = (
    <Menu style={{ width: 200 }}>
      <Menu.Item key="done" onClick={handleDelete}>
        {ready}
      </Menu.Item>
      <Menu.Divider />
      <Menu.Item key="cancel" onClick={() => setConfirmVisible(true)}>
        {cancel}
      </Menu.Item>
      <Menu.Divider />
      <Menu.Item key="changeDate" onClick={() => setTaskToEdit(task)}>
        {edit}
      </Menu.Item>
    </Menu>
  );

  const handleSubmit = (e) => {
    e.preventDefault();
    validateFields((err, values) => {
      if (!err) {
        addSubtask(
          sphere,
          task,
          {
            id: uuid(),
            plan: values.subtask,
            date: "21.02.20",
            time: "09:00",
            isEveryday: false,
            deadline: task.deadline,
            sphere,
          },
          period,
          exactDate,
          sphere
        );
        resetFields();
      }
    });
  };
  const getCurrentSubtasks = () => {
    const sphereTasks = switchTasksSphere(
      sphere,
      healthTasks,
      learningTasks,
      mentalTasks,
      relationshipTasks,
      affairsTasks
    );

    const taskFromState =
      sphereTasks &&
      sphereTasks[period] &&
      sphereTasks[period][exactDate] &&
      sphereTasks[period][exactDate].find((object) => object.id === task.id);

    return taskFromState ? taskFromState.subtasks : [];
  };

  return (
    <div>
      <Row gutter={[20, 20]}>
        <Col span={21}>
          <Col span={21}>
            <Dropdown
              overlay={cardDropdown}
              trigger={["click"]}
              placement="bottomLeft"
            >
              <TaskTitle>
                {task.title}
                <IconButton type="more" />
              </TaskTitle>
            </Dropdown>
          </Col>
          <Col span={3}>
            <CardThinText>{task.deadline}</CardThinText>
          </Col>
          <Col span={24}>
            <TaskTextWrapper>
              <RegularCardText>{task.description}</RegularCardText>
            </TaskTextWrapper>
          </Col>
        </Col>
        <Column offset={1} span={1}>
          <TaskIconsWrapper>
            <MediumText>0%</MediumText>
            <TaskVerticalProgress percent={0} />
          </TaskIconsWrapper>
        </Column>

        <Column span={1}>
          <IconsSider style={{ padding: "15px 0 10px 0" }}>
            <NotificationIcon />
            <BiggerListIcon
              style={{ cursor: "pointer" }}
              onClick={() => setSubtasksOpen(!areSubtasksOpen)}
            />
            <DollarIcon />
          </IconsSider>
        </Column>
      </Row>
      <Expand open={areSubtasksOpen} duration={300} transitions={transitions}>
        <Row>
          <Divider />
          <Form onSubmit={handleSubmit}>
            <Form.Item>
              {getFieldDecorator("subtask", {
                rules: [
                  {
                    required: true,
                    message: "Пожалуйста, введите, что Вам необходимо сделать!",
                  },
                ],
              })(
                <TaskInput
                  placeholder="Что мне необходимо сделать"
                  prefix={<AddIcon />}
                />
              )}
            </Form.Item>
          </Form>

          <Col span={24}>
            <SubtasksList listToRender={getCurrentSubtasks()} />
          </Col>
        </Row>
      </Expand>
      <ConfirmCancelModal
        visible={confirmVisible}
        setVisible={setConfirmVisible}
        cancelTask={handleDelete}
      />
      <DeadlineCalendar
        setDeadline={() => {}}
        setTimeLeft={() => {}}
        isRangeCalendarOpen={isCalendarVisible}
        setRangeCalendarOpen={setCalendarVisible}
        numberOfMonths={1}
      />
    </div>
  );
};

const IconsSider = styled(TaskIconsWrapper)`
  svg {
    fill: url(#gradient);
  }
`;

const TaskTextWrapper = styled.div`
  text-align: justify;
`;

const mapStateToProps = (state) => ({
  healthTasks: state.healthTasks,
  learningTasks: state.learningTasks,
  mentalTasks: state.mentalTasks,
  relationshipTasks: state.relationshipTasks,
  affairsTasks: state.affairsTasks,
});

const mapDispatchToProps = (dispatch) => ({
  addSubtask: (sphere, task, subtask, period, exactDate) =>
    dispatch(AddSubtaskActionCreator(sphere, task, subtask, period, exactDate)),
  deleteTask: (sphere, period, date, id) =>
    dispatch(DeleteTaskActionCreator(sphere, period, date, id)),
});

const WrappedTaskCard = Form.create({ name: "task_card" })(TaskCard);

export default withNamespaces()(
  connect(mapStateToProps, mapDispatchToProps)(WrappedTaskCard)
);
