import React from "react";
import { Row, Col } from "antd";
import styled from "styled-components";

import DayPlan from "./components/DayPlan";
import WeekPlan from "./components/WeekPlan";
import MonthPlan from "./components/MonthPlan";
import YearPlan from "./components/YearPlan";
import { PageTitle } from "../../common/StyledComponents";

const Home = () => {
  return (
    <PageSizeRow gutter={[14, 0]}>
      <Col offset={1} span={23}>
        <PageTitle>River</PageTitle>
      </Col>
      <Col span={13}>
        <Row>
          <Col span={24} style={{ marginBottom: "10px" }}>
            <WeekPlan />
          </Col>
          <Col span={24}>
            <Row gutter={[14, 0]}>
              <Col span={12}>
                <MonthPlan />
              </Col>
              <Col span={12}>
                <YearPlan />
              </Col>
            </Row>
          </Col>
        </Row>
      </Col>
      <Col span={11}>
        <DayPlan />
      </Col>
    </PageSizeRow>
  );
};

const PageSizeRow = styled(Row)`
  height: 100%;
  padding-right: 60px;
`;

export default Home;
