import React from "react";
import styled from "styled-components";
import { useHistory, useLocation } from "react-router-dom";
import moment from "moment";
import { List } from "antd";

const years = [
  {
    title: moment().year(),
  },
  {
    title: moment().add(5, "year").year(),
  },
  {
    title: moment().add(10, "year").year(),
  },
];

const ListWithYears = ({ selectYear }) => {
  const history = useHistory();
  const { pathname } = useLocation();
  const handleYearClick = (year) => {
    selectYear(year);
    history.push(`${pathname}?period=year`);
  };
  return (
    <YearsList
      grid={{ gutter: 16, column: 1 }}
      dataSource={years}
      renderItem={(item) => (
        <ListItem onClick={() => handleYearClick(item.title)}>
          {item.title}
        </ListItem>
      )}
    />
  );
};

const ListItem = styled(List.Item)``;

const YearsList = styled(List)`
  font-size: 44px;
  color: #3b3b3b;
  .ant-list-item {
    padding-left: 20px;
    font-weight: 600;
    line-height: 65px;
    letter-spacing: -2px;
    cursor: pointer;
    :hover {
      background: #1d4e89;
      color: #fff;
      border-radius: 5px 0px 0px 5px;
    }
  }
`;

export default ListWithYears;
