import * as R from "ramda";

export const addNewTask = (state, payload, tasksName) => {
  const {period, date, task} = payload;

  if (R.isNil(state[period][date])) {
    state[period] = {
      [date]: [task],
      ...state[period],
    };
  } else {
    state[period][date].unshift(task);
  }
  localStorage.setItem(tasksName, JSON.stringify(state));
  return state;
};

export const addNewSubtask = (state, payload, tasksName) => {
  const {period, exactDate, task} = payload;
  const updatedObjectForExactDate =
    state &&
    state[period] &&
    state[period][exactDate].map((stateTask) => {
      if (stateTask.id === task.id) {
        return {
          ...stateTask,
          subtasks: [payload.subtask, ...stateTask.subtasks],
        };
      } else {
        return stateTask;
      }
    });

  state[period][exactDate] = updatedObjectForExactDate;
  localStorage.setItem(tasksName, JSON.stringify(state));
  return state;
};

export const deleteTask = (state, payload, tasksName) => {
  const {period, date} = payload;
  if (state[period][date]) {
    const periodWithoutDeletedTask = state[period][date].filter(
      (task) => task.id !== payload.id
    );
    state[period][date] = periodWithoutDeletedTask;
    localStorage.setItem(tasksName, JSON.stringify(state));
  }
  return state;
};

export const deleteSubtask = (state, payload, tasksName) => {
  const {period, exactDate} = payload;
  const updatedObjectForExactDate =
    state &&
    state[period] &&
    state[period][exactDate].map((object) => {
      return {
        ...object,
        subtasks: object.subtasks.filter(
          (subtask) => subtask.id !== payload.subtask.id
        ),
      };
    });

  state[period][exactDate] = updatedObjectForExactDate;
  localStorage.setItem(tasksName, JSON.stringify(state));
  return state;
};

export const editTask = (state, payload, tasksName) => {
  const { period, date, task } = payload;
  if (state[period][date]) {
    state[period][date] = state[period][date].map((stateTask) =>
      stateTask.id === task.id ? task : stateTask
    );
    localStorage.setItem(tasksName, JSON.stringify(state));
  }
  return state;
};
