import React from "react";
import styled from "styled-components";
import { Col, Menu, Layout } from "antd";
import { NavLink } from "react-router-dom";

import { ReactComponent as MailIcon } from "../../assets/envelope.svg";
import { ReactComponent as UserIcon } from "../../assets/user.svg";
import { ReactComponent as SettingsIcon } from "../../assets/settings-menu.svg";
import RedLogo from "../../assets/redLogo.png";
import RetinaRedLogo from "../../assets/redLogo@2x.png";

const { Header } = Layout;

const PageHeader = () => (
  <TransparentHeader>
    <NavLink to="/profile">
      <RedLogoLink
        src={RedLogo}
        srcSet={`${RedLogo},
            ${RetinaRedLogo} 2x`}
        alt="logo"
      />
    </NavLink>
    <HeaderCol offset={20} span={4}>
      <HeaderMenu mode="horizontal">
        <Menu.Item key="mail">
          <MailIcon />
        </Menu.Item>
        <Menu.Item key="user">
          <UserIcon />
        </Menu.Item>
        <Menu.Item key="settings">
          <NavLink to="/settings">
            <SettingsIcon />
          </NavLink>
        </Menu.Item>
      </HeaderMenu>
    </HeaderCol>
  </TransparentHeader>
);

const TransparentHeader = styled(Header)`
  background: #fff;
  position: relative;
  height: 10vh;
  min-height: 100px;
`;

const HeaderCol = styled(Col)`
  margin-top: 30px;
`;

const HeaderMenu = styled(Menu)`
  li.ant-menu-item.ant-menu-item-selected,
  li.ant-menu-item.ant-menu-item-active {
    border-bottom: 2px solid #bebdbd;
  }
  border-bottom: none;
`;

const RedLogoLink = styled.img`
  position: absolute;
  padding-top: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  max-width: 95px;
`;

export default PageHeader;
