import React from "react";
import styled from "styled-components";

import { TaskAmountText } from "../../common/StyledComponents";
import { ReactComponent as FlagIcon } from "../../assets/flag.svg";
import { ReactComponent as ListIcon } from "../../assets/clipboard.svg";

const TasksSummaryInIcons = ({
  tasksLength,
  subtasksLength,
  doneTasksLength
}) => {
  return (
    <TasksSummaryWrapper
      justify={
        tasksLength >= 0 && subtasksLength >= 0 ? "space-around" : "flex-end"
      }
    >
      {tasksLength >= 0 && (
        <TaskAmountText>
          <FlagIcon /> {tasksLength}
        </TaskAmountText>
      )}
      {subtasksLength >= 0 && (
        <TaskAmountText>
          <ListIcon /> {subtasksLength}
        </TaskAmountText>
      )}
      {/* {doneTasksLength >= 0 && (
        <TaskAmountText>
          <MarkIcon /> {doneTasksLength}
        </TaskAmountText>
      )} */}
    </TasksSummaryWrapper>
  );
};

const TasksSummaryWrapper = styled.div`
  width: 100%;
  display: flex;
  justify-content: ${(prop) => prop.justify};
`;

export default TasksSummaryInIcons;
