import React from "react";
import { Row, Col, Form } from "antd";
import moment from "moment";
import { connect } from "react-redux";
import "moment/locale/ru";
import "moment/locale/uk";
import "moment/locale/en-gb";
import styled from "styled-components";
import { withNamespaces } from "react-i18next";

import { InfoCard, SubTitle } from "../../../../common/StyledComponents";
import YearsList from "./components/YearsList";
import {
  AddTaskActionCreator,
  AddSubtaskActionCreator,
} from "../../../../redux/actionCreators/TasksActionCreators";
import InfoList from './components/InfoList';

const EnterSpherePage = ({ setYear, t }) => {
  moment.locale(t("lang"));
  const { globalAim, alreadyHave, globalAimMockedText, alreadyHaveMockedText } = t("enterSphere", { returnObjects: true });

  return (
    <>
      <Row gutter={[30, 20]}>
        <Col span={10}>
          <GlobalAimCard>
            <SubTitle>{globalAim}</SubTitle>
            <p>
              {globalAimMockedText}
            </p>
          </GlobalAimCard>
        </Col>
        <Col span={10}>
          <CurrentPositionCard>
            <SubTitle>{alreadyHave}</SubTitle>
            <p>
              {alreadyHaveMockedText}
            </p>
          </CurrentPositionCard>
        </Col>
        <Col span={4}>
          <YearsList selectYear={setYear} />
        </Col>
        <Col span={24}>
          <InfoList />
        </Col>
      </Row>
    </>
  );
};

const CurrentPositionCard = styled(InfoCard)`
  border-left: 3px solid #1d4e89;
  h3 {
    color: #1d4e89;
  }
`;

const GlobalAimCard = styled(InfoCard)`
  border-left: 3px solid #f85c71;
`;

const mapStateToProps = (state) => ({
  healthTasks: state.healthTasks,
  learningTasks: state.learningTasks,
  mentalTasks: state.mentalTasks,
  relationshipTasks: state.relationshipTasks,
  affairsTasks: state.affairsTasks,
});

const mapDispatchToProps = (dispatch) => ({
  addTask: (sphere, newTask, period, task) =>
    dispatch(AddTaskActionCreator(sphere, newTask, period, task)),
  addSubtask: (sphere, task, subtask, period, exactDate) =>
    dispatch(AddSubtaskActionCreator(sphere, task, subtask, period, exactDate)),
});

const WrappedEnterSpherePage = Form.create({ name: "weekday_subtasks" })(
  EnterSpherePage
);

export default withNamespaces()(
  connect(mapStateToProps, mapDispatchToProps)(WrappedEnterSpherePage)
);
