import React from "react";
import styled from "styled-components";
import { withNamespaces } from "react-i18next";

import { ReactComponent as EmptyIcon } from "../../../../assets/emptyGoal.svg";

const EmptyTasks = ({ t }) => {
  const { emptyPhilosophyText } = t("home", { returnObjects: true });
  return (
    <WrapperWithText>
      <TransparentEmptyIcon />
      <TextAbove>{emptyPhilosophyText}</TextAbove>
    </WrapperWithText>
  );
};

const TransparentEmptyIcon = styled(EmptyIcon)`
  width: 35vw;
  height: 45vh;
  opacity: 0.1;
`;

const WrapperWithText = styled.div`
  position: relative;
`;

const TextAbove = styled.p`
  position: absolute;
  top: 40%;
  left: 35%;
  font-weight: 300;
  font-size: 42px;
  color: #000000;
`;

export default withNamespaces()(EmptyTasks);
