import React from "react";
import { Col, Row } from "antd";

import {
    PeriodTitle
  } from "../../../common/StyledComponents";
import TasksSummaryInIcons from "../../../components/TasksLengthRow/TasksSummaryInIcons";
const PeriodHeader = ({ period, tasksLength }) => {
  return (
    <Row style={{marginBottom: 15}}>
      <Col span={19}>
        <PeriodTitle>{period}</PeriodTitle>
      </Col>
      <Col span={5}>
        <TasksSummaryInIcons tasksLength={tasksLength} />
      </Col>
    </Row>
  );
};

export default PeriodHeader;
