import React, { useState } from "react";
import moment from "moment";
import DayPicker from "react-day-picker";
import "react-day-picker/lib/style.css";
import styled from "styled-components";
import { useHistory, useLocation } from "react-router-dom";

const getWeekRange = (date) => {
  return {
    from: moment(date).startOf("week").toDate(),
    to: moment(date).endOf("week").toDate(),
  };
};

const MonthCalendar = ({
  setWeek,
  week,
  setDay,
  day,
  monthData,
  setWeekNumber,
}) => {
  const history = useHistory();
  const { pathname } = useLocation();
  const [hoverRange, setHoverRange] = useState("");
  const [selectedDays, setSelectedDays] = useState(day ? [day] : week);

  const handleDayChange = (date) => {
    if (moment(date).month() === moment(monthData.monthForCalendar).month()) {
      setWeek([]);
      setDay(date);
      setSelectedDays([date]);
      history.push(`${pathname}?period=day`);
    }
  };

  const handleDayEnter = (date) => {
    setHoverRange(getWeekRange(date));
  };

  const handleDayLeave = () => {
    setHoverRange("");
  };

  const handleWeekClick = (week, days, event) => {
    setDay("");
    setWeek(days);
    setSelectedDays(days);
    var weekContent = window
      .getComputedStyle(event.target, ":before")
      .getPropertyValue("content");
    setWeekNumber(weekContent.replace(/'|"/g, ""));
    history.push(`${pathname}?period=week`);
  };

  const daysAreSelected = week.length > 0;

  const modifiers = {
    hoverRange,
    selectedRange: daysAreSelected && {
      from: week[0],
      to: week[6],
    },
    hoverRangeStart: hoverRange && hoverRange.from,
    hoverRangeEnd: hoverRange && hoverRange.to,
    selectedRangeStart: daysAreSelected && week[0],
    selectedRangeEnd: daysAreSelected && week[6],
    sundays: { daysOfWeek: [6] },
    thursdays: { daysOfWeek: [0] },
  };

  const modifiersStyles = {
    sundays: {
      color: "#F85C71",
    },
    thursdays: {
      color: "#F85C71",
    },
  };

  return (
    <div className="SelectedWeekExample">
      <MonthsSidebarCalendar
        firstDayOfWeek={1}
        month={monthData.monthForCalendar}
        oneDaySelected={selectedDays.length === 1}
        showWeekNumbers
        showOutsideDays
        canChangeMonth={false}
        onWeekClick={handleWeekClick}
        selectedDays={selectedDays}
        modifiers={modifiers}
        modifiersStyles={modifiersStyles}
        onDayClick={handleDayChange}
        onDayMouseEnter={handleDayEnter}
        onDayMouseLeave={handleDayLeave}
      />
    </div>
  );
};

const MonthsSidebarCalendar = styled(DayPicker)`
  overflow: visible;
  * {
    outline: none !important;
    border: none !important;
  }
  .DayPicker-Caption,
  .DayPicker-NavButton,
  .DayPicker-Weekday {
    display: none;
  }
  div.DayPicker-Day {
    font-weight: 300;
    color: #3b3b3b;
    height: 43px;
    width: 47px;
    padding: 0;
    font-size: 14px;
    line-height: 17px;
  }
  .DayPicker-Day--selected {
    border-radius: ${(props) => (props.oneDaySelected ? "50%" : "0")};
    border: none;
    color: #3b3b3b !important;
    background-color: #f85c71 !important;
  }
  .DayPicker-Month {
    margin: 0 !important;
  }
  .DayPicker-WeekNumber ~ .DayPicker-Day--selected {
    border: 20px solid red;
  }

  .DayPicker-WeekNumber {
    font-size: 0;
    width: 40px;
  }

  div.DayPicker-WeekNumber::before {
    font-size: 14px;
    color: #3b3b3b;
    margin-right: 15px;
  }

  div.DayPicker-Week:nth-of-type(1) {
    .DayPicker-WeekNumber::before {
      content: "I";
    }
  }

  div.DayPicker-Week:nth-of-type(2) {
    .DayPicker-WeekNumber::before {
      content: "II";
    }
  }

  div.DayPicker-Week:nth-of-type(3) {
    .DayPicker-WeekNumber::before {
      content: "III";
    }
  }

  div.DayPicker-Week:nth-of-type(4) {
    .DayPicker-WeekNumber::before {
      content: "IV";
    }
  }

  div.DayPicker-Week:nth-of-type(5) {
    .DayPicker-WeekNumber::before {
      content: "V";
    }
  }
  div.DayPicker-Week:nth-of-type(6) {
    .DayPicker-WeekNumber::before {
      content: "VI";
    }
  }

  .DayPicker-Months {
    justify-content: start;
  }
  .DayPicker-Day--selectedRange {
    background-color: #f85c71 !important;
  }
  .DayPicker-Day--outside {
    font-size: 13px !important;
    opacity: 0.5;
  }
`;

export default MonthCalendar;
