import React, { useState } from "react";
import { Modal, Row, Col, message } from "antd";
import DayPicker, { DateUtils } from "react-day-picker";
import "react-day-picker/lib/style.css";
import styled from "styled-components";
import moment from "moment";
import { withNamespaces } from "react-i18next";

import {
  PrimaryButton,
  CancelButton,
  NormalText,
  Column,
} from "../../common/StyledComponents";

const DeadlineCalendar = ({
  isRangeCalendarOpen,
  setRangeCalendarOpen,
  numberOfMonths,
  setDeadline,
  setTimeLeft,
  t,
}) => {
  const getInitialState = {
    from: new Date(),
    to: undefined,
  };

  const [range, setRange] = useState(getInitialState);

  const handleDayClick = (day) => {
    const datesRange = DateUtils.addDayToRange(day, range);
    setRange(datesRange);
  };

  const handleCancelClick = () => {
    setRange(getInitialState);
    setRangeCalendarOpen(false);
  };

  const { from, to } = range;
  const modifiers = { start: from, end: to };

  const handleSave = () => {
    to ? setDeadline(to.toLocaleDateString()) : message.error(setTerms);
    to && setRangeCalendarOpen(false);
    from && to && setTimeLeft(moment(to).diff(from, "days"));
  };

  const { setTerms } = t("aim", { returnObjects: true });

  return (
    <GradientModal
      width={numberOfMonths === 1 ? 440 : 655}
      visible={isRangeCalendarOpen}
      title={numberOfMonths === 1 ? "Дата начала" : "Дата начала и завершения"}
      onOk={() => setRangeCalendarOpen(false)}
      onCancel={() => setRangeCalendarOpen(false)}
      footer={[]}
    >
      <Row>
        <Col span={24}>
          <DeadlineDayPicker
            className="Selectable"
            canChangeMonth={numberOfMonths === 2}
            numberOfMonths={numberOfMonths}
            selectedDays={[from, { from, to }]}
            modifiers={modifiers}
            onDayClick={handleDayClick}
          />
        </Col>
        <Col
          offset={numberOfMonths === 1 ? 4 : 0}
          span={numberOfMonths === 1 ? 20 : 10}
          style={{ margin: "22px 0" }}
        >
          {to && (
            <LittleNormalText>
              Дата завершения:{" "}
              <LittleNormalText strong>
                {to.toLocaleDateString()}
              </LittleNormalText>
            </LittleNormalText>
          )}
        </Col>
        {numberOfMonths === 2 && (
          <Col span={4} style={{ margin: "22px 0" }}>
            <LittleNormalText>
              <BottomBorderedText>
                {from && to ? moment(to).diff(from, "days") : 0}
              </BottomBorderedText>{" "}
              дней
            </LittleNormalText>
          </Col>
        )}
        <Column span={numberOfMonths === 1 ? 12 : 5}>
          <LittlePrimaryButton key="submit" type="primary" onClick={handleSave}>
            Сохранить
          </LittlePrimaryButton>
        </Column>
        <Column span={numberOfMonths === 1 ? 12 : 5}>
          <LittleCancelButton key="back" onClick={handleCancelClick}>
            Отмена
          </LittleCancelButton>
        </Column>
      </Row>
    </GradientModal>
  );
};

const DeadlineDayPicker = styled(DayPicker)`
  font-size: 12px;
  line-height: 14px;
  width: 100%;
  .DayPicker-Months {
    justify-content: space-around;
    height: 225px;
  }

  .DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside) {
    background-color: #eee;
    color: #000;
    opacity: 0.87;
    border-radius: 0;
  }

  .DayPicker-Day--start:not(.DayPicker-Day--outside),
  .DayPicker-Day--end:not(.DayPicker-Day--outside) {
    background-color: #1d4e89 !important;
    color: #fff !important;
    border-radius: 50% 50% 50% 50% !important;
  }
`;

const LittleNormalText = styled(NormalText)`
  font-size: 16px;
  line-height: 18px;
`;

const LittlePrimaryButton = styled(PrimaryButton)`
  padding: 0px 20px;
  span {
    font-size: 16px;
    line-height: 18px;
  }
`;

const LittleCancelButton = styled(CancelButton)`
  padding: 0px 20px;
  span {
    font-size: 16px;
    line-height: 18px;
  }
`;

const GradientModal = styled(Modal)`
  .ant-modal-header {
    background: linear-gradient(90deg, #f85c71 0%, #1d4e89 96.19%);
    max-height: 90px;
    height: 90px;
    padding-top: 20px;
  }
  .ant-modal-title {
    display: flex;
    justify-content: center;
    text-align: center;
    font-weight: 800;
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
    box-sizing: border-box;
  }
  .ant-modal-footer {
    display: none;
  }
`;

const BottomBorderedText = styled.span`
  border-bottom: 1px solid #3b3b3b;
  font-size: 20px;
`;

export default withNamespaces()(DeadlineCalendar);
