import React from "react";
import { VictoryPie } from "victory";

const Pie = ({ openTasksList }) => {
  return (
    <svg viewBox="0 0 400 400">
      <VictoryPie
        standalone={false}
        colorScale={["#1D4E89", "#F85C71", "#3B3B3B"]}
        data={[
          { x: 1, y: 135 },
          { x: 2, y: 180 },
          { x: 3, y: 45 }
        ]}
        style={{
          data: {
            stroke: "#fff",
            strokeWidth: 14,
            cursor: "pointer",
          },
          labels: {
            fontSize: 0
          }
        }}
        events={[
          {
            target: "data",
            eventHandlers: {
              onClick: () => {
                return [
                  {
                    target: "data",
                    mutation: () => {
                      openTasksList && openTasksList();
                    }
                  }
                ];
              }
            }
          }
        ]}
      />
    </svg>
  );
};

export default Pie;
