import React from "react";
import styled from "styled-components";
import { Row, Col } from "antd";
import { withNamespaces } from "react-i18next";

import {
  ProfileCard,
  ProfileCardText,
} from "../../../../common/StyledComponents";
import { ReactComponent as SuccessIcon } from "../../../../assets/success.svg";
import AvatarUploader from "./AvatarUploader";

const UserCard = ({
  t,
  firstName,
  lastName,
  mindfullnessLevel,
  email,
  occupation,
}) => {
  const { mindfullness, location, activity } = t("profile", {
    returnObjects: true,
  });

  return (
    <ProfileCard>
      <Row type="flex">
        <Col span={9} style={{ minWidth: "fit-content" }}>
          <AvatarUploader />
        </Col>
        <Col span={15}>
          <ProfileCardText>{mindfullness}</ProfileCardText>
          <BoldText>{mindfullnessLevel ? mindfullnessLevel : 0}</BoldText>
          <SuccessIcon /> <LoginText>{email}</LoginText>
          <PersonText>{`${firstName} ${lastName}`}</PersonText>
          <PersonText>{location}</PersonText>
          <PersonText>{occupation ? occupation : activity}</PersonText>
        </Col>
      </Row>
    </ProfileCard>
  );
};

const LoginText = styled.p`
  font-size: 13px;
  line-height: 18px;
  margin: 10px 0 10px 0;
  font-weight: 500;
`;

const PersonText = styled.p`
  font-size: 13px;
  line-height: 18px;
  margin: 3px 0;
  font-weight: 300;
`;

const BoldText = styled.span`
  font-weight: 600;
  font-size: 18px;
  letter-spacing: -2px;
  color: #3b3b3b;
  padding-left: 5px;
  padding-right: 9px;
  border-left: ${(prop) => `1px solid ${prop.bordercolor}`};
`;

export default withNamespaces()(UserCard);
