import { combineReducers } from "redux";
import LearningTasksReducer from "./tasks/LearningTasksReducer";
import HealthTasksReducer from "./tasks/HealthTasksReducer";
import RelationshipTasksReducer from "./tasks/RelationshipTasksReducer";
import AffairsTasksReducer from "./tasks/AffairsTasksReducer";
import MentalTasksReducer from "./tasks/MentalTasksReducer";
import AuthReducer from "./auth/AuthReducer";
import LanguageReducer from "./language/LanguageReducer";

const MainReducer = combineReducers({
  learningTasks: LearningTasksReducer,
  healthTasks: HealthTasksReducer,
  relationshipTasks: RelationshipTasksReducer,
  affairsTasks: AffairsTasksReducer,
  mentalTasks: MentalTasksReducer,
  auth: AuthReducer,
  language: LanguageReducer,
});

export default MainReducer;
