import React, { useState, useEffect } from "react";
import { Col } from "antd";
import { connect } from "react-redux";
import { useHistory, useLocation } from "react-router-dom";
import { v4 as uuid } from "uuid";
import * as R from "ramda";
import Expand from "react-expand-animated";

import {
  BlackText,
  CardWithShadow,
  PageTitle,
  Sticky,
  OverflowContent,
  ColWithRightTextAlign,
  CenteredRow,
} from "../../../../common/StyledComponents";
import TaskCard from "../common/TaskCard/TaskCard";
import TasksSummaryInIcons from "../../../../components/TasksLengthRow/TasksSummaryInIcons";
import AddTaskCard from "../common/AddTaskForm/AddTaskCard";
import { switchTasksSphere, updateTasks } from "../common/helpers";
import { transitions } from "../../../../common/helpers";
import ExpandableAddTaskButton from "../common/ExpandableAddTaskButton";
import EmptyTasks from '../common/EmptyTasks';

const SelectedYearPage = ({
  year,
  sphere,
  healthTasks,
  learningTasks,
  mentalTasks,
  relationshipTasks,
  affairsTasks,
  pageTitle
}) => {
  const history = useHistory();
  const { pathname } = useLocation();

  const currentSphere = switchTasksSphere(
    sphere,
    healthTasks.year,
    learningTasks.year,
    mentalTasks.year,
    relationshipTasks.year,
    affairsTasks.year
  );

  const [isTaskFormOpen, setTaskFormOpen] = useState(false);
  const [currentTasks, setCurrentTasks] = useState(
    currentSphere[year] ? currentSphere[year] : []
  );
  const [taskToEdit, setTaskToEdit] = useState(undefined);

  if (!year) {
    history.push(`${pathname}`);
  }

  const inputTask = () => {
    setTaskFormOpen(true);
  };

  useEffect(() => {
    updateTasks(setCurrentTasks, currentSphere, year);
  }, [
    healthTasks,
    learningTasks,
    mentalTasks,
    relationshipTasks,
    year,
    affairsTasks,
    currentSphere,
    taskToEdit,
  ]);

  return (
    <>
      <Sticky>
        <Col span={24}>
          <PageTitle>{pageTitle}</PageTitle>
        </Col>
        <CenteredRow>
          <Col offset={1} span={16}>
            <ExpandableAddTaskButton
              isTaskFormOpen={isTaskFormOpen}
              inputTask={inputTask}
            />
          </Col>
          <ColWithRightTextAlign span={3}>
            <TasksSummaryInIcons
              tasksLength={currentTasks ? currentTasks.length : 0}
              subtasksLength={0}
              doneTasksLength={0}
            />
          </ColWithRightTextAlign>
          <ColWithRightTextAlign offset={1} span={3}>
            <BlackText>{year}</BlackText>
          </ColWithRightTextAlign>
        </CenteredRow>
      </Sticky>

      <OverflowContent
        offset={1}
        span={23}
        style={{ marginTop:"5vh" }}
      >
        <Expand
          open={isTaskFormOpen || Boolean(taskToEdit)}
          duration={300}
          transitions={transitions}
        >
          <AddTaskCard
            setvisible={setTaskFormOpen}
            sphere={sphere}
            period="year"
            year={year}
            taskToEdit={taskToEdit}
            setTaskToEdit={setTaskToEdit}
          />
        </Expand>
        {R.isEmpty(currentTasks) && !isTaskFormOpen && !Boolean(taskToEdit) ? (
          <EmptyTasks />
        ) : (
          currentTasks.map(
            (task) =>
              (taskToEdit && taskToEdit.id) !== task.id && (
                <CardWithShadow
                  key={uuid()}
                  onClick={() =>
                    updateTasks(setCurrentTasks, currentSphere, year)
                  }
                >
                  <TaskCard
                    task={task}
                    sphere={sphere}
                    year={year}
                    taskToEditId={taskToEdit ? taskToEdit.id : ""}
                    setTaskToEdit={setTaskToEdit}
                  />
                </CardWithShadow>
              )
          )
        )}
      </OverflowContent>
    </>
  );
};

const mapStateToProps = (state) => ({
  healthTasks: state.healthTasks,
  learningTasks: state.learningTasks,
  mentalTasks: state.mentalTasks,
  relationshipTasks: state.relationshipTasks,
  affairsTasks: state.affairsTasks,
});

export default connect(mapStateToProps)(SelectedYearPage);
